import React, { useContext, useState } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom/cjs/react-router-dom.min";
import {
  assignLeadsToSelectedUserPost,
  fetchWAMessageData,
  getAllUsers,
} from "../../api/LMSDashboard";
import * as actions from "../../Store/actions/index";
import { cancelToken } from "../../Store/reducer/LMSDashboard";
import { useLoader } from "../LoaderContext";
import { useToastMessage } from "../ToastMessageContext";
import { v4 as uuid } from "uuid";
import { FetchChatPagePermission } from "api/LeadDashboard";

export const LeadProviderContext = React.createContext();

export const useLead = () => {
  return useContext(LeadProviderContext);
};

const LeadContext = ({
  children,
  storeLead,
  isDepartmentHead,
  leads,
  page,
  loading,
  hasMore,
  allLeads,
  leadCount,
  currentLeadId,
  location,
  history,
  selectedBusiness,
  currentLeadData,
  clearFullLeadDetail,
  fullLeadLoader,
  ...props
}) => {
  const { setErrorMessage, setSuccessMessage } = useToastMessage();
  const toastMessage = useToastMessage();
  const [selectedLeads, setSelectedLeads] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [members, setMembers] = useState([]);
  const [isOpenAssignPage, setOpenAssignPage] = useState(false);
  const [isOpenDownloadPanel, setOpenDownloadPanel] = useState(false);
  const [isDepartment, setDepartment] = useState(false);
  const [departmentExist, isDepartmentExist] = useState(false);
  const [leadData, setLeadData] = useState(null);
  const [leadId, setLeadId] = useState(null);
  const [openFilterSidebar, setOpenFilterSidebar] = useState(false);
  const [isDisableAgentReply] = useState(true);

  const [wa_data, set_wa_data] = useState(null);

  const [chatLoading, setChatLoading] = useState(false);

  const [reload, setReload] = useState(false);

  const [openUserResponsePopup, setOpenUserResponsePopup] = useState(false);

  const [isUserAssignPageLoading, startUserAssignPageLoading] = useState(false);

  const { startLoader, cancelLoader } = useLoader();
  const [permissions, setPermissions] = useState();

  const getChatPagePermissions = async () => {
    try {
      const res = await FetchChatPagePermission("chat");
      if (!res.success) return setErrorMessage(res.message);
      return setPermissions(res.payload?.chat || null);
    } catch (err) {
      return setErrorMessage("Internal server error!");
    }
  };

  const getWAmessageData = async () => {
    try {
      const res = await fetchWAMessageData();

      if (!res.success) return setErrorMessage(res.message);

      return set_wa_data(res.payload);
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const checkDepartment = async (
    page,
    currentTabName,
    { filter },
    toastMessage
  ) => {
    startLoader();
    try {
      isDepartmentExist(false);
      setDepartment(false);

      const departmentId = localStorage.getItem("departmentId") || null;
      const teamId = localStorage.getItem("teamId") || null;

      // const res = await fetchIsDepartmentHead(departmentId, teamId);

      if (teamId || departmentId) {
        isDepartmentExist(true);
        setDepartment(true);
        (!props.departmentLeads || props.departmentAllLeads) &&
          props.getDepartmentLeads(
            page,
            currentTabName,
            { filter },
            toastMessage
          );
      } else {
        setDepartment(false);
        (!leads || allLeads) &&
          props.getLMSLeads(page, currentTabName, { filter }, toastMessage);

        return isDepartmentExist(false);
      }
    } catch (err) {
      isDepartmentExist(false);
      setDepartment(false);
      cancelLoader();
      return setErrorMessage(err.message);
    }
  };

  const handleDepartmentSwitch = (currentTabName, { filter }, toastMessage) => {
    setDepartment(!isDepartment);

    if (!isDepartment) {
      if (
        currentTabName === "all" &&
        props.departmentAllLeads &&
        props.departmentAllLeads.length !== 0
      ) {
        return;
      }

      if (!props.departmentLeads || props.departmentAllLeads) {
        return props.getDepartmentLeads(
          props.departmentPage[currentTabName] || 1,
          currentTabName,
          { filter },
          toastMessage
        );
      }
      return;
    }

    if (!leads || allLeads) {
      if (currentTabName === "all" && allLeads && allLeads.length !== 0) {
        return;
      }

      return props.getLMSLeads(
        page[currentTabName] || 1,
        currentTabName,
        { filter },
        toastMessage
      );
    }
  };

  const fetchAllUsers = async (businessId = undefined) => {
    try {
      startUserAssignPageLoading(true);
      const res = await getAllUsers(businessId);

      if (res.error) {
        startUserAssignPageLoading(false);

        return setErrorMessage(res.error);
      }

      startUserAssignPageLoading(false);

      return setMembers(
        res.members.sort((a, b) => {
          if (a.name && b.name) {
            const nameA = a.name.toUpperCase(); // ignore upper and lowercase
            const nameB = b.name.toUpperCase(); // ignore upper and lowercase
            if (nameA < nameB) {
              return -1;
            }
            if (nameA > nameB) {
              return 1;
            }
          }

          // names must be equal
          return 0;
        })
      );
    } catch (err) {
      startUserAssignPageLoading(false);

      return setErrorMessage(err.message);
    }
  };

  const reAssignLeadsToSelectedUser = async (currentTabName, cb) => {
    try {
      const res = await assignLeadsToSelectedUserPost({
        leads: selectedLeads,
        user_id: selectedUser,
      });

      setSelectedUser(null);
      setSelectedLeads([]);

      if (!res.success) return setErrorMessage(res.message);

      if (!cb) {
        clearLeads();
        clearAllLeads();
        resetLMSPageCount();
        resetLMSLeadCount();

        getLMSLeads(1, currentTabName, { filter: {} }, toastMessage);
      } else {
        cb();
      }

      setSuccessMessage("Leads successfully assigned!");
      return !cb && history.push(`${location.pathname}${location.hash}`);
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const isSelectedLead = ({ leadId }) => {
    const selected = selectedLeads.find((e) => e === leadId);
    return selected ? true : false;
  };

  const addLeadsToAssign = ({ leadId, onlySelect = false }) => {
    if (!isSelectedLead({ leadId }))
      return setSelectedLeads([...selectedLeads, leadId]);

    if (onlySelect) return;
    return setSelectedLeads(selectedLeads.filter((e) => e !== leadId));
  };

  const getLMSLeads = (page, tab, { filter }) => {
    if (isDepartment) {
      return props.getDepartmentLeads(
        page || 1,
        tab,
        { filter },
        toastMessage,
        cancelToken
      );
    }
    return props.getLMSLeads(
      page || 1,
      tab,
      { filter },
      toastMessage,
      cancelToken
    );
  };

  const clearAllLeads = () => {
    if (isDepartment) return props.departmentClearAllLeads();
    return props.clearAllLeads();
  };

  const clearLeads = () => {
    if (isDepartment) return props.departmentClearLeads();
    return props.clearLeads();
  };

  const resetLMSLeadCount = () => {
    if (isDepartment) return props.resetDepartmentLeadCount();
    return props.resetLMSLeadCount();
  };

  const resetLMSPageCount = () => {
    if (isDepartment) return props.resetDepartmentPageCount();
    return props.resetLMSPageCount();
  };

  const getFullLeadData = (leadId, toastMessage, showLoader = true) => {
    return props.getFullLeadData(leadId, toastMessage, showLoader);
  };

  const leadsProvider = () => {
    if (isDepartment) return props.departmentLeads;
    return leads;
  };

  const allLeadsProvider = () => {
    if (isDepartment) return props.departmentAllLeads;
    return allLeads;
  };

  const loadingProvider = () => {
    if (isDepartment) return props.departmentLoading;
    return loading;
  };

  const hasMoreProvider = () => {
    if (isDepartment) return props.departmentHasMore;
    return hasMore;
  };

  const leadCountProvider = () => {
    if (isDepartment) return props.departmentLeadCount;
    return leadCount;
  };

  const pageProvider = () => {
    if (isDepartment) return props.departmentPage;
    return page;
  };

  const getLeadData = ({ leadId, currentTab, store = true }) => {
    if (currentTab.toLowerCase() === "all") {
      const lead = allLeadsProvider().find((e) => e.id === leadId);
      if (store) setLeadData(lead);
      return lead;
    }

    const lead = leadsProvider().find((e) => e.id === leadId);
    if (store) setLeadData(lead);
    return lead;
  };

  const moveLeadToTopOnNewMessage = (
    lead,
    leadId,
    currentChatUserLeadId = null
  ) => {
    if (currentChatUserLeadId) {
      if (currentChatUserLeadId === leadId) {
        setLeadData((prevState) => ({
          ...prevState,
          is_chat_initiated: true,
          lead_data: {
            ...prevState.lead_data,
            updatedAt: new Date(),
          },
          last_message: { ...lead.last_message },
        }));
      }
    }

    return props.moveLeadToTopOnNewMessage(lead, leadId);
  };

  const startLoadingChat = (boolean) => {
    return setChatLoading(boolean);
  };

  const updateComponent = () => {
    return setReload(uuid());
  };

  const updateLeadDetail = ({ data }) => {
    return props.updateLeadDetail({
      data,
      toastMessage,
      leadId: currentLeadId,
    });
  };

  const updateLead = ({ data }) => {
    return props.updateLead({
      data,
      toastMessage,
      leadId: currentLeadId,
    });
  };

  const updateLeadItem = (leadId, leadItem) => {
    return props.updateLeadItem(leadId, leadItem);
  };

  const updateLMSLeadCount = () => {
    props.updateLMSLeadCount("all");

    return updateComponent();
  };

  const value = {
    updateLMSLeadCount,
    updateLead,
    updateLeadItem,
    updateLeadDetail,
    updateComponent,
    isOpenAssignPage,
    setOpenAssignPage,
    isOpenDownloadPanel,
    setOpenDownloadPanel,
    reAssignLeadsToSelectedUser,
    members,
    setMembers,
    selectedUser,
    fetchAllUsers,
    setSelectedUser,
    isSelectedLead,
    selectedLeads,
    setSelectedLeads,
    addLeadsToAssign,
    leads: leadsProvider(),
    page: pageProvider(),
    loading: loadingProvider(),
    getLMSLeads,
    storeLead,
    isDepartmentHead,
    hasMore: hasMoreProvider(),
    allLeads: allLeadsProvider(),
    leadCount: leadCountProvider(),
    currentLeadId,
    getFullLeadData,
    clearLeads,
    clearAllLeads,
    resetLMSPageCount,
    resetLMSLeadCount,
    selectedBusiness,
    currentLeadData,
    clearFullLeadDetail,
    fullLeadLoader,
    checkDepartment,
    setDepartment,
    isDepartment,
    handleDepartmentSwitch,
    departmentExist,
    getLeadData,
    leadData,
    openFilterSidebar,
    setOpenFilterSidebar,
    openUserResponsePopup,
    setOpenUserResponsePopup,
    leadNotes: props.leadNotes,
    isUserAssignPageLoading,
    startUserAssignPageLoading,
    moveLeadToTopOnNewMessage,
    reload,
    setReload,
    updateCurrentStatus: props.updateCurrentStatus,
    chatLoading,
    startLoadingChat,
    setLeadId,
    leadId,
    addLeadNotes: props.addLeadNotes,
    setLeadData,
    isDisableAgentReply,
    getChatPagePermissions,
    getWAmessageData,
    wa_data,
    permissions,
  };

  return (
    <LeadProviderContext.Provider value={value}>
      {children}
    </LeadProviderContext.Provider>
  );
};

const mapStateToProps = (state) => {
  return {
    currentLeadId: state.LMSDashboard.currentLeadId,
    leads: state.LMSDashboard.leads,
    page: state.LMSDashboard.page,
    loading: state.LMSDashboard.loading,
    hasMore: state.LMSDashboard.hasMore,
    allLeads: state.LMSDashboard.allLeads,
    leadCount: state.LMSDashboard.leadCount,
    selectedBusiness: state.business.selectedBusiness,
    currentLeadData: state.LMSDashboard.currentLeadData,
    fullLeadLoader: state.LMSDashboard.fullLeadLoader,
    departmentLeads: state.department.leads,
    departmentAllLeads: state.department.allLeads,
    departmentLoading: state.department.loading,
    departmentName: state.department.departmentName,
    departmentLeadCount: state.department.leadCount,
    departmentHasMore: state.department.hasMore,
    departmentPage: state.department.page,
    leadNotes: state.lead.notes,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getLMSLeads: (page, tab, { filter }, toastMessage, cancelToken) =>
      dispatch(
        actions.getLMSLeads(page, tab, { filter }, toastMessage, cancelToken)
      ),
    storeLead: (leadId, data, toastMessage) =>
      dispatch(actions.storeLMSLead(leadId, data, toastMessage)),
    isDepartmentHead: (departmentId, teamId) =>
      dispatch(actions.isDepartmentHead(departmentId, teamId)),
    getFullLeadData: (leadId, toastMessage, showLoader) =>
      dispatch(actions.getFullLeadData(leadId, toastMessage, showLoader)),
    clearLeads: () => dispatch(actions.clearLeads()),
    clearAllLeads: () => dispatch(actions.clearAllLeads()),
    resetLMSPageCount: () => dispatch(actions.resetLMSPageCount()),
    resetLMSLeadCount: () => dispatch(actions.resetLMSLeadCount()),
    clearFullLeadDetail: () => dispatch(actions.clearFullLeadDetail()),
    getDepartmentLeads: (page, tab, { filter }, toastMessage, cancelToken) =>
      dispatch(
        actions.getDepartmentLeads(
          page,
          tab,
          { filter },
          toastMessage,
          cancelToken
        )
      ),
    departmentClearLeads: () => dispatch(actions.clearAllDepartmentLeads()),
    departmentClearAllLeads: () => dispatch(actions.clearAllDepartmentLeads()),
    resetDepartmentPageCount: () =>
      dispatch(actions.resetDepartmentPageCount()),
    resetDepartmentLeadCount: () =>
      dispatch(actions.resetDepartmentLeadCount()),
    moveLeadToTopOnNewMessage: (lead, leadId) =>
      dispatch(actions.moveLeadToTopOnNewMessage(lead, leadId)),
    updateCurrentStatus: (data, leadId, toastMessage) =>
      dispatch(actions.updateCurrentStatus(data, leadId, toastMessage)),
    updateLeadDetail: ({ data, leadId, toastMessage }) =>
      dispatch(actions.updateLeadDetail({ data, leadId, toastMessage })),
    updateLead: ({ data, leadId, toastMessage }) =>
      dispatch(actions.updateLead({ data, leadId, toastMessage })),
    addLeadNotes: ({ notes }) => dispatch(actions.addLeadNotes({ notes })),
    updateLeadItem: (leadId, leadItem) =>
      dispatch(actions.updateLeadItem(leadId, leadItem)),
    updateLMSLeadCount: (tabName) =>
      dispatch(actions.updateLMSLeadCount(tabName)),
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(LeadContext)
);
