/** @format */

import { db } from "../../../firebase";
import { authSteps, authState } from "../auth/auth";
import { cancelLoading, error, startLoading } from "../main";
import * as actions from "../actionTypes";
import { getUserProfile, updateUserProfile } from "../../../api/user";
import { fetchUserPayments } from "../../../api/Order";

export const getUserDetail = (user) => {
  return {
    type: actions.GET_USER_DETAIL,
    user: user,
  };
};
export const storeUserPayments = (payments) => {
  return {
    type: actions.STORE_USER_PAYMENTS,
    payments: payments,
  };
};

export const fetchPaymentsByUser = (history) => {
  return async (dispatch) => {
    dispatch(startLoading());
    try {
      const payments = await fetchUserPayments();
      if (payments.error) {
        dispatch(error(payments.error));
        return history.goBack();
      }
      dispatch(storeUserPayments(payments.data));
      dispatch(cancelLoading());
    } catch (err) {
      dispatch(error(err.message));
      return history.goBack();
    }
  };
};

export const checkUserDetail = () => {
  return (dispatch) => {
    dispatch(startLoading());
    const userData = db.collection("userData");
    const userId = localStorage.getItem("userId");

    userData
      .where("userId", "==", userId)
      .where("Name", "==", null)
      .where("Email", "==", null)
      .get()
      .then((user) => {
        if (user.empty) {
          dispatch(authSteps(3));
          dispatch(authState(true));
          dispatch(cancelLoading());
        } else {
          dispatch(authSteps(2));
          dispatch(authState(false));
          dispatch(cancelLoading());
        }
      })
      .catch((err) => {
        dispatch(error(err.message));
      });
  };
};

export const updateUserData = (name, email) => {
  return (dispatch) => {
    dispatch({ type: actions.UPDATE_USER_DATA });
    dispatch(startLoading());

    return updateUserProfile({ name: name, email: email })
      .then((res) => {
        if (res.error) {
          dispatch(error(res.error));
          return alert(res.error);
        }

        dispatch(cancelLoading());
        dispatch(authSteps(3));
      })
      .catch((err) => {
        return dispatch(error(err.message));
      });
  };
};

export const fetchUserDetail = () => {
  return (dispatch) => {
    dispatch(startLoading());
    getUserProfile()
      .then((user) => {
        if (user.error) {
          return dispatch(error(user.error));
        }
        dispatch(getUserDetail(user.data));
        return dispatch(cancelLoading());
      })
      .catch((err) => {
        dispatch(error(err.message));
      });
  };
};
