/** @format */

import {
  createOrder,
  captureOrder,
  fetchOrder,
  saveOrderOnDatabase,
} from "../../../api/Order";
import { authSteps } from "../auth/auth";
import { KEY } from "../../../Backend";
import { createBusiness, createFreeBusiness } from "../../../api/Business";
import { cancelLoading, error, startLoading } from "../main";
import { getPlanDetail } from "../../../api/Plan";

export const saveOrder = (
  order,
  amount,
  couponDetail,
  gstNumber,
  gstPercentage,
  gstAmount
) => {
  return (dispatch) => {
    dispatch(startLoading());
    console.log("Saving Order....");

    const planId = JSON.parse(localStorage.getItem("planId"));
    // Get Plan Details
    getPlanDetail(planId)
      .then((planDetail) => {
        sessionStorage.setItem("planDetail", JSON.stringify(planDetail.data));

        const paymentId = JSON.parse(
          sessionStorage.getItem("paymentData")
        ).paymentId;

        const address = JSON.parse(sessionStorage.getItem("address"));

        if (order && amount > 0) {
          saveOrderOnDatabase({
            order: order,
            plan: {
              expiry: new Date(
                new Date().getFullYear(),
                new Date().getMonth(),
                new Date().getDate() + planDetail.data.details.validity
              ),
              start: new Date(),
              planId: planId,
            },
            gstInfo: {
              gstPercentage,
              gstAmount,
            },
            gstNumber: gstNumber,
            couponDetail,
          })
            .then((order) => {
              // Order is now saved
              console.log("order saved");

              // Creating new Business
              createBusiness({
                paymentId: paymentId,
                planId: planId,
                address: address,
                validity: planDetail.data.details.validity,
              })
                .then((data) => {
                  console.log("business saved");
                  sessionStorage.setItem("businessId", data.id);
                  dispatch(authSteps(6));
                  dispatch(cancelLoading());
                })
                .catch((err) => dispatch(error(err)));
            })
            .catch((err) => error(err));
        } else {
          createFreeBusiness({
            planId: planId,
            address: address,
            validity: planDetail.data.details.validity,
          })
            .then((data) => {
              sessionStorage.setItem("businessId", data.id);
              dispatch(authSteps(6));
              dispatch(cancelLoading());
            })
            .catch((err) => dispatch(error(err)));
        }
      })
      .catch((err) => {
        dispatch(error(err));
      });
  };
};

export const orderHandler = (
  amount,
  planName,
  name,
  couponDetail,
  gstNumber,
  gstPercentage,
  gstAmount
) => {
  return (dispatch) => {
    dispatch(startLoading());
    // Free plan processing
    if (amount === 0) {
      const paymentData = {
        amount: amount,
        name: name,
        status: 1,
      };

      // Saving free plan details to session storage
      sessionStorage.setItem("paymentData", JSON.stringify(paymentData));
      // Success page redirection
      dispatch(saveOrder(null, amount));
    } else {
      // Creating order if price > 0 RS
      createOrder({
        // amount of the order
        amount: amount,
      })
        .then((amount) => {
          // order created
          const options = {
            key: KEY,
            name: "Easy Social",
            description: planName,
            order_id: amount.id,
            handler: async (response) => {
              try {
                // paymentId
                const paymentId = response.razorpay_payment_id;
                // Capturing order
                captureOrder(paymentId, {
                  amount: amount,
                })
                  .then((response) => {
                    // fetching order
                    fetchOrder(paymentId)
                      .then((data) => {
                        console.log("Fetching Order....");
                        const paymentData = {
                          amount: amount.amount / 100,
                          paymentId: paymentId,
                          name: name,
                          status: 1,
                        };
                        sessionStorage.setItem(
                          "paymentData",
                          JSON.stringify(paymentData)
                        );
                        dispatch(
                          saveOrder(
                            data,
                            amount.amount / 100,
                            couponDetail,
                            gstNumber,
                            gstPercentage,
                            gstAmount
                          )
                        );
                      })
                      .catch((err) => {
                        const paymentData = {
                          amount: amount.amount / 100,
                          status: 0,
                        };
                        sessionStorage.setItem(
                          "paymentData",
                          JSON.stringify(paymentData)
                        );
                        dispatch(authSteps(7));
                        dispatch(error(err));
                      });
                  })
                  .catch((err) => dispatch(err(err)));
              } catch (err) {
                const paymentData = {
                  amount: amount.amount / 100,
                  status: 0,
                };
                sessionStorage.setItem("paymentData", JSON.parse(paymentData));
                dispatch(authSteps(7));
                dispatch(error(err));
              }
            },
            theme: {
              color: "#686CFD",
            },
          };
          const rzp1 = new window.Razorpay(options);
          rzp1.open();
        })
        .catch((err) => dispatch(error(err)));
    }
  };
};
