const Routes = ({ authRedirectPath, ...props }) => {
  return (
    <>
      <Switch>
        <Route path="/excel-test" component={AsyncExcelTestPage} exact />
        <Route
          path="/chat-flow/:chatflow_id"
          component={AsyncChatFlowDashboard}
          exact
        />
        <PrivateRoute
          path="/engage/broadcast"
          component={AysncCampaignPage}
          exact
        />
        {/* business settings routes */}
        <PrivatePermitRoute
          path="/business-settings"
          component={AsyncMobileBusinessSettings}
          exact
        />
        {/*         <PrivateRoute
        path="/business/general-settings"
        component={AsyncBusinessSettings}
        exact
      /> */}
        {/*         <Route
        path="/business/social-linking"
        component={AsyncSocialLinking}
        exact
      /> */}
        <PrivatePermitRoute
          path="/business/team-members"
          component={AsyncTeamMembers}
          exact
        />
        <PrivatePermitRoute
          path="/business/lead-data-fields"
          component={AsyncLeadDataFields}
          exact
        />
        <PrivatePermitRoute
          path="/business/lead-notifications"
          component={AsyncLeadNotification}
          exact
        />
        <PrivatePermitRoute
          path="/business/wa-usage-report"
          component={AsyncWhatsapp}
          exact
        />
        <PrivatePermitRoute
          path="/business/wa-template-messages"
          component={AsyncTemplateMessages}
          exact
        />
        <PrivatePermitRoute
          path="/business/wa-business-settings"
          component={AsyncWABusinessProfileSettings}
          exact
        />
        <PrivatePermitRoute
          path="/business/wa-integrations"
          component={AsyncWABusinessIntegrations}
          exact
        />
        <PrivatePermitRoute
          path="/business/wa-integrations/:integration_id"
          component={AsyncIntegrationDetail}
          exact
        />
        <PrivatePermitRoute
          path="/business/lead-distribution-rules"
          component={AsyncLeadDistributionRules}
          exact
        />
        {/*Business settings routes ends here */}
        <PrivatePermitRoute
          path="/business/general-settings"
          component={AsyncBusinessSettings}
          exact
        />
        <PrivatePermitRoute
          path="/business/social-linking"
          component={AsyncSocialLinking}
          exact
        />
        <PrivatePermitRoute
          path="/all-payments"
          component={AsyncMobilePayments}
          exact
        />
        <PrivatePermitRoute
          path="/all-payments/invoices"
          component={AsyncAllPaymentsInvoices}
          exact
        />
        <PrivatePermitRoute
          path="/all-payments/orders"
          component={AsyncAllPaymentsOrders}
          exact
        />
        <PrivatePermitRoute
          path="/all-payments/wallet"
          component={AsyncAllPaymentsWallet}
          exact
        />
        <PrivatePermitRoute
          path="/all-payments/subscriptions/:businessId?"
          component={AsyncAllPaymentsSubscriptions}
          exact
        />

        {/* Engage menu */}
        <PrivateRoute
          path="/engage/lead-dashboard"
          component={AsyncLeadDashboard}
          exact
        />
        <PrivateRoute
          path="/engage/analytics"
          component={AsyncAnalytics}
          exact
        />
        <PrivateRoute path="/engage/chat" component={AsyncLMSDashboard} exact />
        {/* Engage menu */}
        {/* Extended Lead Dashboard */}
        <PrivateRoute
          path="/lead-dashboard-ext"
          component={AsyncLeadDashboardExtended}
          exact
        />
        <PrivateRoute
          path="/curate/calendar-old"
          component={AsyncCalendarDashboard}
          exact
        />
        <PrivateRoute
          path="/curate/calendar"
          component={AsyncCalendarDashboardDev}
          exact
        />
        <PrivateRoute
          path="/engage/analytics/:dashboardId"
          component={AsyncAnalytics}
          exact
        />
        <PrivateRoute
          path="/dashboard/create-post"
          component={AsyncCreatePost}
          exact
        />
        <PrivateRoute
          path="/account/lms-rules/:businessId"
          component={AsyncLMSRules}
          exact
        />
        <PrivateRoute
          path="/account/system-schedule/:businessId"
          component={AsyncSystemSchedulePage}
        />
        <PrivateRoute
          path="/lead-user/:leadId"
          component={AsyncLeadUserPage}
          exact
        />
        <Route path="/i/:invitationId" component={AsyncInvitationPage} exact />
        <PrivateRoute
          path="/business/:businessId/team-members/invite"
          component={AsyncBusinessTeamMembersInviteForm}
          exact
        />
        <PrivateRoute
          path="/business/:businessId/team-members"
          component={AsyncBusinessTeamMembers}
          exact
        />
        <PrivateRoute
          path="/business/linkedIn-connect"
          component={AsyncConnectLinkedIn}
        />
        {/* <PrivateRoute
        path="/business/:businessId/linkedIn-connect"
        component={AsyncConnectLinkedIn}
      /> */}
        {/* <PrivateRoute
        path="/business/:businessId/twitter-connect"
        component={AsyncConnectTwitter}
      /> */}
        <PrivateRoute
          exact
          path="/business/twitter-connect"
          component={AsyncConnectTwitter}
        />
        <PrivateRoute
          path="/business/:businessId/instagram-connect"
          component={AsyncConnectInstagram}
        />
        <PrivateRoute
          path="/business/:businessId/select-page"
          component={AsyncSelectFacebookPage}
        />
        {/* <PrivateRoute
        path="/business/:businessId/fb-insta-connect"
        component={AsyncFacebookInstaConnect}
      /> */}
        <PrivateRoute
          path="/business/fb-insta-connect"
          component={AsyncFacebookInstaConnect}
        />
        {/* <PrivateRoute
        path="/business/:businessId/social-account-setup"
        component={AsyncSocialAccountSetupSuccess}
      /> */}
        <PrivateRoute
          path="/business/social-account-setup"
          component={AsyncSocialAccountSetupSuccess}
        />
        <PrivateRoute
          path="/business/facebook-connect"
          component={AsyncFacebookLogin}
        />
        <PrivateRoute
          path="/user/payments/renewals"
          component={AsyncUserPaymentRenewals}
        />
        <PrivateRoute
          path="/user/payments/order-renewal/summary"
          component={AsyncUserPaymentRenewalsSummary}
        />
        <PrivateRoute
          path="/user/payments/history"
          component={AsyncUserPaymentsHistory}
        />
        <PrivateRoute path="/user/payments" component={AsyncUserPayments} />
        {/* <PrivateRoute
        path="/business/:businessId/social-profiles"
        component={AsyncBusinessSocialProfiles}
      /> */}
        <PrivateRoute
          path="/business/social-profiles"
          component={AsyncBusinessSocialProfiles}
        />
        <PrivateRoute
          path="/business/:businessId/account-detail"
          component={AsyncBusinessAccountDetail}
        />
        <PrivateRoute
          path="/business/:business_name/:businessId/detail"
          component={AsyncBusinessDetailMenu}
          exact
        />
        <PrivateRoute path="/user/profile" component={AsyncMyProfile} exact />
        <PrivateRoute
          path="/template/upload/finish"
          component={AsyncTemplateDetail}
        />
        <Route
          path="/template/success/:templateId"
          component={AsyncTemplateSuccess}
        />
        <Route
          path="/template/edit/:templateId"
          component={AsyncEditTemplate}
        />
        <Route path="/create/template" component={AsyncCreateTemplate} />
        <Route path="/load-template/:templateId" component={AsyncCanvas} />
        <PrivateRoute path="/account/bill" component={AsyncPlanSummary} exact />
        <PrivateRoute
          path="/business-setup/:businessId/success"
          component={AsyncAccountSuccess}
          exact
        />
        <PrivateRoute
          path="/business-data"
          component={AsyncBusinessData}
          exact
        />
        {/* Social-Accounts Setup Routes */}
        <PrivateRoute
          path="/business-setup/:businessId/link-linkedIn"
          component={AsyncLinkedIn}
          exact
        />
        <PrivateRoute
          path="/business-setup/:businessId/link-twitter"
          component={AsyncTwitter}
          exact
        />
        <PrivateRoute
          path="/business-setup/:businessId/link-instagram"
          component={AsyncInstagram}
          exact
        />
        <PrivateRoute
          path="/business-setup/link-facebook"
          component={AsyncFacebook}
          exact
        />
        <PrivateRoute
          path="/business-setup/:businessId/select-page"
          component={AsyncSelect_page}
          exact
        />
        {/* Business Routes */}
        <PrivateRoute
          path="/business-setup/:businessId/basic-setup"
          component={AsyncAccountDetail}
          exact
        />
        <PrivateRoute
          path="/business-setup/:businessId/select-location"
          component={AsyncLocation}
          exact
        />
        <PrivateRoute
          path="/payment/success"
          component={AsyncPaymentSuccess}
          exact
        />
        <PrivateRoute
          path="/payment/failed"
          component={AsyncPaymentFailure}
          exact
        />
        <PrivateRoute
          path="/choose-plans/billing"
          component={AsyncBillingDetail}
          exact
        />
        <PrivateRoute path="/choose-plans" component={AsyncPlans} exact />
        <PrivateRoute
          path="/login/enter-info"
          component={AsyncFormEmailDetail}
          exact
        />
        <Route path="/signout" component={AsyncSignout} exact />
        <Route exact path="/forgot-password" component={AsyncForgotPassword} />
        <Route exact path="/custom-login" component={AsyncCustomLogin} />
        <Route path="/login/:number/enter-otp" component={AsyncLogin} exact />
        {/* <Route exact path="/" component={AsyncHomepage} />
              <Route path="/contact" component={AsyncContactPage} />
              <Route
                  exact
                  path="/whatsapp-chatbot"
                  component={AsyncWhatsappChatbotPage}
              />

              <Route path="/login" component={AsyncLogin} exact /> */}
        {/* New Login Route */}
        <Route path="/login" component={AsyncLoginPage} exact />
        <Route path="/" component={DefaultRedirect} exact />
        {/* <Route path="/festival" component={AsyncHondaLoginPage} /> */}
        {/* Home */}
        {/* <Route exact path="/" component={AsyncWelcomeHome} /> */}
        {/* Pricing */}
        {/* <Route exact path="/pricing" component={AsyncPricing} /> */}
        {/* Engage */}
        {/* <Route exact path="/engage" component={AsyncEngage} /> */}
        {/* Privacy Policy */}
        {/* <Route exact path="/privacy-policy" component={AsyncPrivacyPolicy} /> */}
        {/* Terms & Conditions */}
        {/* <Route exact path="/tnc" component={AsyncTermsAndConditions} /> */}
        {/* Contact */}
        {/* <Route path="/contact" component={AsyncWelcomeContact} /> */}
        {/* WhatsApp Chatbot */}
        {/* <Route path="/whatsapp-chatbot" component={AsyncWelcomeWAChatbot} /> */}
        {/* Blogs Routes */}
        {/* <Route exact path="/blogs" component={AsyncBlogList} /> */}
        {/* <Route path="/blogs/:title" component={AsyncBlogDetail} /> */}
        <Route exact path="/sign-up" component={AsyncNewSignup} />
        <PrivateRoute
          exact
          path="/sign-up-success"
          component={AsyncSignupSuccess}
        />
        <PrivateRoute
          exact
          path="/business-setup"
          component={AsyncBusinessSetup}
        />
        <Route
          exact
          path="/business-setting-resume"
          component={AsyncBusinessSetupResume}
        />
        <Route exact path="/choose-plan" component={AsyncChoosePlan} />
        {/* <Route exact path="/billing-detail" component={AsyncNewBillingDetail} /> */}
        <PrivateRoute
          exact
          path="/payment-success"
          component={AsyncPrePaymentSuccess}
        />
        <PrivateRoute
          exact
          path="/payment-failed"
          component={AsyncPrePaymentFailed}
        />
        <PrivateRoute
          exact
          path="/getting-started"
          component={AsyncGettingStarted}
        />
        <Route exact path="/unauthorized" component={AsyncUnAuthorized} />
        <Route path="*" component={AsyncNotFound} />
        <Redirect to="/login" />
      </Switch>
      {/* {formComponents()} */}
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    authStep: state.auth.currentCount,
    authRedirectPath: state.auth.authRedirectPath,
    defaultRedirectPath: state.auth.defaultRedirectPath,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    authSteps: (currentCount) => dispatch(actions.authSteps(currentCount)),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Routes));
