// import * as actions from "../actionTypes";
import {
  checkBusinessOrderStatus,
  saveBasicInfo as saveInfo,
  updateSteps,
} from "../../../api/Business/index";
import { authSteps } from "../../actions/auth/auth";
import { storeLocation } from "../../../api/Business/index";
import { cancelLoading, error, startLoading } from "../main";
import {
  updateAddress,
  checkForAddress as checkAddress,
} from "../../../api/Address";
import * as actions from "../../actions/actionTypes";
import { getAllBusiness, getBusinessProfile } from "../../../api/Dashboard";
import { fetchIsDepartmentHead } from "../../../api/Auth";
import {
  clearAllDepartmentLeads,
  clearDepartmentLeads,
  resetDepartmentLeadCount,
  resetDepartmentPageCount,
} from "../department/department";
import {
  clearAllLeads,
  clearLeads,
  resetLMSLeadCount,
  resetLMSPageCount,
} from "../LMSDashboard/LMSDashboard";

//import { useSidebar } from "../../../contexts/SidebarProvider/SidebarProvider";

export const getAllUserBusiness = (allBusiness) => {
  return {
    type: actions.GET_ALL_USER_BUSINESS,
    allBusiness: allBusiness,
  };
};

export const getDefaultBusiness = (defaultBusiness) => {
  return {
    type: actions.GET_DEFAULT_BUSINESS,
    defaultBusiness: defaultBusiness,
  };
};

export const storeBusinessOrders = (businessOrders) => {
  return {
    type: actions.STORE_BUSINESS_ORDERS,
    businessOrders: businessOrders,
  };
};

export const getUserBusiness = () => {
  return (dispatch) => {
    dispatch(startLoading());
    getAllBusiness()
      .then((res) => {
        if (res.error) {
          return dispatch(error(res.error));
        }

        dispatch(getAllUserBusiness(res.payload));
        dispatch(cancelLoading());
      })
      .catch((err) => dispatch(error(err)));
  };
};

export const saveBasicInfo = (businessId, data) => {
  return (dispatch) => {
    dispatch(startLoading());
    saveInfo(businessId, data)
      .then((data) => {
        dispatch(authSteps(9));
        dispatch(cancelLoading());
      })
      .catch((err) => {
        dispatch(error(err));
        dispatch(cancelLoading());
      });
  };
};

// saveLocation
export const saveLocation = (businessId, data) => {
  return (dispatch) => {
    dispatch(startLoading());

    storeLocation(businessId, data)
      .then((data) => {
        dispatch(authSteps(10));
        dispatch(cancelLoading());
      })
      .catch((err) => {
        dispatch(error(err));
        dispatch(cancelLoading());
      });
  };
};

export const addAddress = (
  name,
  address,
  pincode,
  city,
  state,
  franchisee_code,
  country
) => {
  return (dispatch) => {
    dispatch(startLoading());
    updateAddress({
      name,
      address,
      pincode,
      city,
      state,
      franchisee_code,
      country,
    })
      .then((data) => {
        if (data.error) {
          dispatch(error(data.error));
          dispatch(cancelLoading());
        } else {
          dispatch(authSteps(5));
          dispatch(cancelLoading());
        }
      })
      .catch((err) => {
        dispatch(error(err));
        dispatch(cancelLoading());
      });
  };
};

export const checkForAddress = () => {
  return (dispatch) => {
    dispatch(startLoading());
    checkAddress()
      .then((data) => {
        if (data.error) {
          dispatch(authSteps(4));
          dispatch(error(data.error));
        } else {
          dispatch(authSteps(5));
          dispatch(cancelLoading());
        }
      })
      .catch((err) => {
        dispatch(error(err));
      });
  };
};

export const updateStep = (businessId, step) => {
  return (dispatch) => {
    updateSteps(businessId, step)
      .then((data) => data)
      .catch((err) => dispatch(error(err)));
  };
};

export const fetchBusinessOrders = (history) => {
  return (dispatch) => {
    dispatch(startLoading());
    checkBusinessOrderStatus()
      .then((payments) => {
        if (payments.error) {
          dispatch(error(payments.error));
          return history.goBack();
        }
        dispatch(storeBusinessOrders(payments.data));
        dispatch(cancelLoading());
      })
      .catch((err) => {
        dispatch(error(err.message));
        return history.goBack();
      });
  };
};

export const isDepartmentHead = (departmentId, teamId) => {
  return (dispatch) => {
    dispatch(startLoading());
    fetchIsDepartmentHead(departmentId, teamId)
      .then((res) => {
        if (res.error) {
          dispatch(cancelLoading());
          return true;
        }
        dispatch(cancelLoading());
        // return (window.location.href = "/dashboard/department");
      })
      .catch((err) => {
        console.log(err.message);
        dispatch(cancelLoading());
      });
  };
};

export const fetchDefaultBusiness = () => {
  return (dispatch) => {
    dispatch(startLoading());
    getBusinessProfile()
      .then((res) => {
        if (!res.success) {
          localStorage.removeItem("businessId");
          // return window.location.reload();
        }

        const { payload } = res;

        localStorage.setItem("businessId", payload.id);

        // temp comment by nagendra
        if (res.payload.departments.length > 0) {
          localStorage.setItem("departmentId", res.payload.departments[0].id);
        } else localStorage.removeItem("departmentId");

        if (res.payload.teams.length > 0) {
          localStorage.setItem("teamId", res.payload.teams[0].id);
        } else localStorage.removeItem("teamId");

        const business = {
          business_name: payload.business_name,
          id: payload.id,
          //url: res.data.url,
          //role: res.data.role,
          redirectUrl: payload.default_url,
          industry: payload.industry,
          address: payload.address,
          // planCategory: res.data.plan_category,
          // ordersDetail: res.data.ordersDetail,
        };

        dispatch(getDefaultBusiness(business));
        return dispatch(cancelLoading());
      })
      .catch((err) => {
        return dispatch(error(err));
      });
  };
};

export const switchBusiness = (businessId, data) => {
  return async (dispatch) => {
    dispatch(clearAllDepartmentLeads());
    dispatch(clearDepartmentLeads());
    dispatch(resetLMSLeadCount());
    dispatch(resetLMSPageCount());
    dispatch(clearLeads());
    dispatch(clearAllLeads());
    dispatch({
      type: actions.GET_FULL_LEAD_DATA,
      currentLeadData: null,
      currentLeadId: null,
    });

    dispatch(resetDepartmentPageCount());
    dispatch(resetDepartmentLeadCount());

    dispatch({ type: "SWITCH_BUSINESS" });

    const business = {
      business_name: data.business_name,
      id: businessId,
      url: data.url,
      role: data.role,
      redirectUrl: data.redirectUrl,
      ...data,
    };

    localStorage.setItem("businessId", businessId);

    if (data.department.id) {
      localStorage.setItem("departmentId", data.department.id);
    } else {
      localStorage.removeItem("departmentId");
    }

    if (data.department.teamId) {
      localStorage.setItem("teamId", data.department.teamId);
    } else {
      localStorage.removeItem("teamId");
    }

    return dispatch(getDefaultBusiness(business));
  };
};
