import { array_move } from "../../Shared/Utility";
import * as actions from "../actions/actionTypes";

const initialState = {
  leads: null,
  allLeads: null,
  currentLeadData: null,
  currentLeadId: null,
  page: {},
  error: false,
  hasMore: false,
  leadCount: {},
  limit: 50,
  fullLeadLoader: false,
};

export let cancelToken;

// Update whole Lead
const updateLead = (state, action) => {
  const { leadId, lead } = action;

  const isExistInAllLead = state.allLeads.findIndex((e) => e.id === leadId);

  if (isExistInAllLead !== -1) {
    const modifiedAllLeads = [...state.allLeads];

    modifiedAllLeads[isExistInAllLead] = { ...lead };

    return {
      ...state,
      allLeads: modifiedAllLeads,
    };
  }

  const isExistInLead = state.leads.findIndex((e) => e.id === leadId);

  if (isExistInLead === -1) {
    return state;
  }

  const modifiedLeads = [...state.leads];

  modifiedLeads[isExistInLead] = lead;

  return {
    ...state,
    leads: modifiedLeads,
  };
};

// Update specific leadItem
const updateLeadItem = (state, action) => {
  const { leadId, leadItem } = action;

  const AllLeadIndex = state.allLeads.findIndex((e) => e.id === leadId);

  if (AllLeadIndex !== -1) {
    const modifiedAllLeads = [...state.allLeads];

    modifiedAllLeads[AllLeadIndex] = {
      ...modifiedAllLeads[AllLeadIndex],
      ...leadItem,
    };

    return {
      ...state,
      allLeads: modifiedAllLeads,
      currentLeadData: {
        ...state.currentLeadData,
        ...leadItem,
      },
    };
  }

  if (!state.leads) {
    return state;
  }

  const leadIndex = state.leads.findIndex((e) => e.id === leadId);

  if (leadIndex === -1) {
    return state;
  }

  const modifiedLeads = [...state.leads];

  modifiedLeads[leadIndex] = {
    ...modifiedLeads[leadIndex],
    ...leadItem,
  };

  return {
    ...state,
    leads: modifiedLeads,
    currentLeadData: { ...state.currentLeadData, ...leadItem },
  };
};

const updateLMSLeadCount = (state, action) => {
  return {
    ...state,
    leadCount: {
      ...state.leadCount,
      [action.tabName]: state.leadCount[action.tabName] + 1 || 1,
    },
  };
};

const getLeads = (state, action) => {
  return {
    ...state,
    leads:
      (state.leads && [...new Set([...state.leads, ...action.leads])]) ||
      action.leads,
    page: { ...state.page, [action.tabName]: action.page || 1 },
    leadCount: { ...state.leadCount, [action.tabName]: action.leadCount },
  };
};

const moveLeadToTopOnNewMessage = (state, action) => {
  if (!state.allLeads) return { ...state };

  const oldIndex = state.allLeads.findIndex((e) => e.id === action.leadId);

  if (oldIndex < 0) {
    return {
      ...state,
      allLeads: [...new Set([action.lead, ...state.allLeads])],
    };
  }

  let stop_bot = {};

  if (typeof action.lead.stop_bot === "boolean" && state.currentLeadData) {
    stop_bot = {
      stop_bot: state.currentLeadData.stop_bot,
    };
  }

  state.allLeads[oldIndex] = {
    ...state.allLeads[oldIndex],
    ...action.lead,
    ...stop_bot,
  };

  const newArray = array_move(state.allLeads, oldIndex, 0);

  return {
    ...state,
    allLeads: newArray,
  };
};

export const getAllLMSLeads = (state, action) => {
  return {
    ...state,
    allLeads:
      (state.allLeads && [...new Set([...state.allLeads, ...action.leads])]) ||
      action.leads,
    page: { ...state.page, [action.tabName]: action.page },
    leadCount: { ...state.leadCount, [action.tabName]: action.leadCount },
  };
};

const addLeads = (state, action) => {
  return {
    ...state,
    leads: action.leads,
  };
};

export const startLoadingLMS = (state, action) => {
  return {
    ...state,
    loading: true,
  };
};

export const getCancelToken = (state, action) => {
  cancelToken = action.cancelToken;
  return { ...state };
};

export const resetLMSLeadCount = (state, action) => {
  return {
    ...state,
    leadCount: {},
  };
};

export const cancelLoadingLMS = (state, action) => {
  return {
    ...state,
    loading: false,
  };
};

export const setHasMore = (state, action) => {
  return {
    ...state,
    hasMore: action.hasMore,
  };
};

export const errorLMS = (state, action) => {
  return {
    ...state,
    loading: false,
    error: action.error,
  };
};

export const getFullLeadData = (state, action) => {
  return {
    ...state,
    currentLeadData: action.currentLeadData,
    currentLeadId: action.currentLeadId,
  };
};

const getCurrentLeadId = (state, action) => {
  return {
    ...state,
    currentLeadId: action.currentLeadId,
  };
};

const setFullLeadLoader = (state, action) => {
  return {
    ...state,
    fullLeadLoader: action.fullLeadLoader ? true : false,
  };
};

export const resetLMSPageCount = (state, action) => {
  return {
    ...state,
    page: {},
  };
};

export const clearLeads = (state, action) => {
  return {
    ...state,
    leads: null,
  };
};

export const clearAllLeads = (state, action) => {
  return {
    ...state,
    allLeads: null,
  };
};

export const clearFullLeadDetail = (state, action) => {
  return {
    ...state,
    currentLeadData: null,
    currentLeadId: null,
  };
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.GET_LEADS:
      return getLeads(state, action);
    case actions.GET_FULL_LEAD_DATA:
      return getFullLeadData(state, action);
    case actions.START_LOADING_LMS:
      return startLoadingLMS(state, action);
    case actions.CANCEL_LOADING_LMS:
      return cancelLoadingLMS(state, action);
    case actions.ERROR_LMS:
      return errorLMS(state, action);
    case actions.HAS_MORE:
      return setHasMore(state, action);
    case actions.ADD_LEADS:
      return addLeads(state, action);
    case actions.GET_ALL_LMS_LEADS:
      return getAllLMSLeads(state, action);
    case actions.RESET_LMS_PAGE_COUNT:
      return resetLMSPageCount(state, action);
    case actions.CLEAR_ALL_LEADS:
      return clearAllLeads(state, action);
    case actions.CLEAR_LEADS:
      return clearLeads(state, action);
    case actions.RESET_LMS_LEAD_COUNT:
      return resetLMSLeadCount(state, action);
    case actions.CLEAR_FULL_LEAD_DETAILS:
      return clearFullLeadDetail(state, action);
    case actions.GET_CANCEL_TOKEN:
      return getCancelToken(state, action);
    case actions.GET_CURRENT_LEAD_ID:
      return getCurrentLeadId(state, action);
    case actions.SET_FULL_LEAD_LOADER:
      return setFullLeadLoader(state, action);
    case actions.MOVE_LEAD_TO_TOP:
      return moveLeadToTopOnNewMessage(state, action);
    case actions.UPDATE_LEAD:
      return updateLead(state, action);
    case actions.UPDATE_LEAD_ITEM:
      return updateLeadItem(state, action);
    case actions.UPDATE_LMS_LEAD_COUNT:
      return updateLMSLeadCount(state, action);
    default:
      return state;
  }
};

export default reducer;
