import React, { useState } from "react";
import { withRouter } from "react-router-dom/cjs/react-router-dom.min";
import { useLead } from "./LeadContext";
import queryString from "query-string";
import { useToastMessage } from "../ToastMessageContext";

export const Tab = React.createContext();

export const useTab = () => {
  return React.useContext(Tab);
};

const TabContext = ({ children, location, history }) => {
  const toastMessage = useToastMessage();

  const { getLMSLeads, page, allLeads } = useLead();

  const [tab, setTabs] = useState([
    {
      label: "All",
      value: "all",
      index: 0,
    },
    {
      label: "New",
      value: "new",
      index: 1,
    },
    {
      label: "Interested",
      value: "interested",
      index: 2,
    },
    { label: "Hot", index: 3, value: "hot" },
    { label: "No Response", index: 4, value: "no_response" },
    { label: "No Interest", index: 5, value: "not_interested" },
    { label: "Closed", index: 6, value: "closed" },
  ]);

  const tabName = tab.findIndex(
    (e) => e.value === location.hash.replace("#", "")
  );

  const currentTabName = tabName !== -1 ? tab[tabName].value : tab[0].value;

  const [activeTab, setActiveTab] = useState(tabName !== -1 ? tabName : 0);

  const onClickTabItem = (index) => {
    const urlQuery = location.search;

    const query = queryString.parse(urlQuery);

    history.push(`${urlQuery}#${tab[index].value}`);

    if (tab[index].label === "All" && allLeads && allLeads.length > 0) {
      return setActiveTab(index);
    }

    if (activeTab !== index) {
      getLMSLeads(
        page[tab[index].value.toLowerCase()],
        tab[index].value,
        { filter: query },
        toastMessage
      );
    }

    return setActiveTab(index);
  };

  const value = {
    activeTab,
    setActiveTab,
    tabName,
    tab,
    setTabs,
    onClickTabItem,
    currentTabName,
  };

  return <Tab.Provider value={value}>{children}</Tab.Provider>;
};

export default withRouter(TabContext);
