import { API_V2 } from "../../../Backend";
import { AuthenticatedHeaders } from "../../../utils/ApiHeaders";

export const getParameters = async () => {
  try {
    const res = await fetch(`${API_V2}/lead-data-fields`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const GetLeadDataFieldsForFilter = async () => {
  try {
    const res = await fetch(`${API_V2}/lead-data-fields/all/filter`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};
