const formatDate = (d) => {
  const [, month, date] = d.split("-");

  const monthList = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Sept",
    "Oct",
    "Nov",
    "Dec",
  ];
  const findEventForDate = `${monthList[month - 1]}-${date}`;

  return findEventForDate;
};

const isAlreadyPresent = (storedData, comingData) => {
  const storedParsedData = JSON.parse(storedData);

  return storedParsedData.events.filter((e) => e.day === comingData.day);
};

const findIndexForElement = (storedData, day) => {
  const storedParsedData = JSON.parse(storedData);

  const findElement = (e) => e.day === day;
  return storedParsedData.events.findIndex(findElement);
};
export { formatDate, isAlreadyPresent, findIndexForElement };
