import { createSlice } from "@reduxjs/toolkit";

const postSlice = createSlice({
  name: "intersect",
  initialState: {
    postList: [],
  },
  reducers: {
    addPost: (state, action) => {
      if (action.payload.type === "ADD") {
        state.postList = [...state.postList, { ...action.payload.post }];
      }

      if (action.payload.type === "FETCH") {
        state.postList = [...action.payload.posts];
      }
    },
    deletePost: (state, action) => {
      const { postID } = action.payload;
      const prevPostList = state.postList.slice();
      const newPostList = prevPostList.filter((e) => e.postID !== postID);
      state.postList = newPostList;
    },
    updatePost: (state, action) => {
      console.log("Update action payload: ", action.payload);

      const { postID } = action.payload;
      const indexToUpdate = state.postList.findIndex(
        (e) => e.postID === postID
      );
      const copyState = state.postList.slice();
      copyState[indexToUpdate] = { ...action.payload };
      state.postList = copyState;
    },
  },
});

export const { addPost, deletePost, updatePost } = postSlice.actions;

export default postSlice.reducer;
