/** @format */

import { cancelLoading, error, startLoading } from "../main";
import { authSteps } from "../auth/auth";
import { saveLinkedInPage } from "../../../api/Auth";

export const storeLinkedInPage = (businessId, selectedPage, profile) => {
  return (dispatch) => {
    dispatch(startLoading());
    saveLinkedInPage(businessId, { selectedPage, profile })
      .then((res) => {
        dispatch(authSteps(15.5));
        dispatch(cancelLoading());
      })
      .catch((err) => {
        dispatch(error(err));
      });
  };
};

export const storeLinkedInPageConnect = (
  businessId,
  selectedPage,
  profile,
  setErrorMessage,
  setSuccessMessage,
  history
) => {
  return (dispatch) => {
    dispatch(startLoading());
    saveLinkedInPage(businessId, { selectedPage, profile })
      .then((res) => {
        dispatch(cancelLoading());
        setSuccessMessage("You Have Successfully linked you LinkedIn Profile.");
        history.push(`/business/${businessId}/social-profiles`);
      })
      .catch((err) => {
        dispatch(error(err));
        setErrorMessage(err);
      });
  };
};
