import CryptoJS from "crypto-js";
import { DECRYPTION_KEY } from "../Backend";

export default function decryptResponse({ data }) {
  if (typeof data === "string") {
    const bytes = CryptoJS.AES.decrypt(data, DECRYPTION_KEY);
    return JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
  } else {
    return data;
  }
}
