import {
  acceptUserInvite,
  getUserInvites,
  sendUserInvite,
  getSearchInvitation,
} from "../../../api/Invite";
import * as actions from "../actionTypes";
import { cancelLoading, error, startLoading } from "../main";

export const getAllUserInvites = (toastMessage, businessId) => {
  return async (dispatch) => {
    dispatch(startLoading());
    try {
      const data = await getUserInvites(businessId);
      if (data.error) {
        return dispatch(error(data.error));
      }
      dispatch(cancelLoading());
      dispatch({
        type: actions.GET_ALL_USER_INVITES,
        invitedUsers: data.data,
      });
    } catch (err) {
      return dispatch(error(err.message));
    }
  };
};

export const searchInvitation = (toastMessage, businessId, searchMember) => {
  return async (dispatch) => {
    //dispatch(startLoading());
    try {
      const data = await getSearchInvitation(businessId, { searchMember });
      if (data.error) {
        dispatch(error(data.error));
        return toastMessage.setErrorMessage(data.error);
      }
      dispatch(cancelLoading());
      dispatch({
        type: actions.GET_ALL_USER_INVITES,
        invitedUsers: data.data,
      });
      /*       dispatch({
        type: actions.GET_SEARCH_INVITES,
        invitedSearchUsers: data.data,
      }); */
    } catch (err) {
      dispatch(error(err.message));
      return toastMessage.setErrorMessage(err.message);
    }
  };
};

export const sendInvite = (data, businessId, toastMessage) => {
  return async (dispatch) => {
    dispatch({ type: actions.SEND_USER_INVITE });
    dispatch(startLoading());
    return sendUserInvite(data, businessId)
      .then((data) => {
        if (data.error) {
          dispatch(error(data.error));
          return toastMessage.setErrorMessage(data.error);
        }
        dispatch(cancelLoading());
        dispatch(getAllUserInvites(toastMessage, businessId));
        return toastMessage.setSuccessMessage(data.message);
      })
      .catch((err) => {
        dispatch(error(err.message));
        return toastMessage.setErrorMessage(err.message);
      });
  };
};

export const acceptInvite = (invitationId, toastMessage) => {
  return async (dispatch) => {
    dispatch({ type: actions.ACCEPT_USER_INVITE });
    return await acceptUserInvite(invitationId)
      .then((data) => {
        if (data.error) {
          dispatch(error(data.error));
          return toastMessage.setErrorMessage(data.error);
        }
        dispatch(cancelLoading());
        return toastMessage.setSuccessMessage(data.message);
      })
      .catch((err) => {
        dispatch(error(err.message));
        return toastMessage.setErrorMessage(err.message);
      });
  };
};
