import React, { useState, useContext } from "react";
import { ToastProvider } from "react-toast-notifications";
import { CustomButtonToast } from "../Components/UI/Toasts/Toast";

export const ToastMessage = React.createContext();

export const useToastMessage = () => {
  return useContext(ToastMessage);
};

export const ToastMessageContext = ({ children }) => {
  const [toast, setToast] = useState({
    error: null,
    success: null,
    info: null,
    disabled: false,
  });

  const setErrorMessage = (value, ms = 500) => {
    setToast({
      success: null,
      info: null,
      error: value,
      disabled: false,
    });

    setTimeout(() => {
      setToast({
        error: null,
        info: null,
        success: null,
        disabled: false,
      });
    }, ms);
  };

  const setSuccessMessage = (value) => {
    setToast({
      success: value,
      info: null,
      error: null,
      disabled: false,
    });

    setTimeout(() => {
      setToast({
        info: null,
        error: null,
        success: null,
        disabled: false,
      });
    }, 500);
  };

  const setInfoMessage = (value, ms = 500) => {
    setToast({
      info: value,
      success: null,
      error: null,
      disabled: false,
    });

    setTimeout(() => {
      setToast({
        info: null,
        error: null,
        success: null,
        disabled: false,
      });
    }, ms);
  };

  const setDisabled = () => {
    setToast({
      info: null,
      error: null,
      success: null,
      disabled: true,
    });
  };

  const value = {
    setSuccessMessage,
    setErrorMessage,
    isDisabled: toast.disabled,
    setDisabled,
    setInfoMessage,
  };
  return (
    <ToastMessage.Provider value={value}>
      {children}
      <ToastProvider>
        <CustomButtonToast
          show={
            toast.error !== null ||
            toast.success !== null ||
            toast.info !== null
          }
          appearance={
            toast.error
              ? "error"
              : toast.success
              ? "success"
              : toast.info
              ? "info"
              : null
          }
          message={toast.error || toast.success || toast.info}
        />
      </ToastProvider>
    </ToastMessage.Provider>
  );
};
