import { createSlice, current, PayloadAction } from "@reduxjs/toolkit";

type SliceState = {
  isLoading?: boolean;
  lead_notes?: { note: string; createdAt: Date; followUp: Date }[];
  leadId?: string | null;
};

const initialState: SliceState = {
  isLoading: false,
  lead_notes: [],
  leadId: null,
};

const leadProfileSlice = createSlice({
  name: "intersect",
  initialState,
  reducers: {
    storeLeadNotes: (
      state: SliceState,
      action: PayloadAction<{ lead_notes: any[]; leadId: string }>
    ) => {
      const { lead_notes, leadId } = action.payload;

      return {
        ...current(state),
        lead_notes: [...state.lead_notes, ...lead_notes],
        leadId,
        isLoading: false,
      };
    },
    handleLeadProfileLoading: (
      state: SliceState,
      action: PayloadAction<{ isLoading: boolean }>
    ) => {
      const { isLoading } = action.payload;

      return {
        ...current(state),
        isLoading,
      };
    },
  },
});

export const { storeLeadNotes, handleLeadProfileLoading } = leadProfileSlice.actions;

export default leadProfileSlice.reducer;
