import React, { useContext, useEffect } from "react";

//import { getUserProfile} from "../../../../api/user";
import { getUserProfile } from "../api/user";

export const LoggedUser = React.createContext();

export const useLoggedUser = () => useContext(LoggedUser);

const LoggedUserContext = ({ children }) => {
  const [userDetail, setUserDetail] = React.useState("");

  useEffect(() => {
    // preload();
  }, []);

  const preload = async () => {
    const res = await getUserProfile();
    if (res.error) {
      console.log(res.error);
    } else {
      setUserDetail(res.data);
    }
  };

  const value = {
    userDetail,
  };

  return <LoggedUser.Provider value={value}>{children}</LoggedUser.Provider>;
};

export default LoggedUserContext;
