import React, { useContext, useState } from "react";

//import { getUserProfile} from "../api/user";
import { useToastMessage } from "../contexts";

import { getAllSubscriptions } from "../api/Subscription/indexBKP";

export const Subscription = React.createContext();

export const useSubscription = () => useContext(Subscription);

const SubscriptionContext = ({ children }) => {
  const { setErrorMessage } = useToastMessage();
  const [defaultOrders, setDefaultOrders] = useState([]);
  const [updatedOrders, setUpdatedOrders] = useState([]);
  const [modifiedOrders, setModifiedOrders] = useState([]);
  const [nextMonthAmount, setNextMonthAmount] = useState(0);
  const [invoiceDetail, setInvoiceDetail] = useState();
  const [paymentStatus, setPaymentStatus] = useState(false);

  //  const [subscriptions, setSubscriptions] = useState([]);
  // const [busiOrder, setBusiOrder] = useState("");

  /*   const getSubscriptions = async () => {
      const res = await getAllSubscriptions();
      if (res.error) {
        setErrorMessage(res.error);
      } else {
        setSubscriptions(res.data);
      }    
  }; */

  const value = {
    defaultOrders,
    setDefaultOrders,
    updatedOrders,
    setUpdatedOrders,
    modifiedOrders,
    setModifiedOrders,
    nextMonthAmount,
    setNextMonthAmount,
    invoiceDetail,
    setInvoiceDetail,
    paymentStatus,
    setPaymentStatus,
    // setBusiOrder
  };

  return (
    <Subscription.Provider value={value}>{children}</Subscription.Provider>
  );
};

export default SubscriptionContext;
