import { useEffect } from "react";
import "./App.css";

import Routes from "./Routes/Routes";

import { connect } from "react-redux";

import Loader from "./Components/UI/Loader/Loader";
import BusinessProfileContext from "./contexts/BusinessProfileContext";

import { ToastMessageContext } from "./contexts/ToastMessageContext";
import LoaderContext from "./contexts/LoaderContext";
import ErrorBoundary from "./hoc/ErrorBoundary";
import { BrowserRouter } from "react-router-dom";
import AuthContext from "./contexts/AuthContext";
import { SocketProvider } from "./contexts/SocketProvider";
import SidebarProvider from "./contexts/SidebarProvider/SidebarProvider";

import AppProvider from "./contexts/AppProvider";
import BusinessProvider from "./contexts/BusinessProvider";
import { QueryClient, QueryClientProvider } from "react-query";

// bootstrap css
import "./Styles/bootstrap/grid.min.css";
import "./Styles/bootstrap/utilities.min.css";

import DailogContext from "./contexts/DailogProvider";
import ModelContext from "./contexts/ModelProvider";

// Social Media Setup Context
import SocialMediaSetupContextProvider from "./contexts/SocialMediaSetupContext";
import WaitingLoaderContext from "./contexts/waitingLoaderContext";
import WhatsAppTemplateContext from "./contexts/WhatsappTemplate";

import LeadDataFieldProvider from "./contexts/LeadDataFieldProvider";
import LeadDistributionRulesProvider from "./contexts/LeadDistributionRulesProvider";
import LeadContext from "./contexts/LMSDashboard/LeadContext";
import TabContext from "./contexts/LMSDashboard/TabContext";
import LMSFilterContext from "./contexts/LMSDashboard/LMSFilterContext";
import LMSDashboardContext from "./contexts/LMSDashboard/LMSDashboardContext";
import AnalyticsProvider from "./contexts/AnalyticsProvider";
import CampaignContext from "./contexts/CampaignProvider";
import TeamMemberContext from "./contexts/TeamMember";
import LoggedUserContext from "./contexts/UserProvider";
import InvoiceContext from "./contexts/InvoiceProvider";
import SubscriptionContext from "./contexts/SubscriptionProvider";

// Create a client
const queryClient = new QueryClient();

function App(props) {
  // First we get the viewport height and we multiple it by 1% to get a value for a vh unit
  let vh = window.innerHeight * 0.01;
  // Then we set the value in the --vh custom property to the root of the document
  document.documentElement.style.setProperty("--vh", `${vh}px`);

  useEffect(() => {
    window.addEventListener("resize", () => {
      // We execute the same script as before
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty("--vh", `${vh}px`);
    });
  }, []);

  return (
    <>
      <QueryClientProvider client={queryClient}>
        <BrowserRouter>
          <ErrorBoundary>
            <div className="App">
              {props.loading ? <Loader /> : null}
              <AppProvider>
                <AuthContext>
                  <LoggedUserContext>
                    <SocketProvider>
                      <BusinessProfileContext>
                        <LoaderContext>
                          <ToastMessageContext>
                            <LeadContext>
                              <TabContext>
                                <LMSFilterContext>
                                  <LMSDashboardContext>
                                    <BusinessProvider>
                                      <AnalyticsProvider>
                                        <SidebarProvider>
                                          <SocialMediaSetupContextProvider>
                                            <CampaignContext>
                                              <DailogContext>
                                                <ModelContext>
                                                  <WaitingLoaderContext>
                                                    <TeamMemberContext>
                                                      <LeadDataFieldProvider>
                                                        <LeadDistributionRulesProvider>
                                                          <WhatsAppTemplateContext>
                                                            <InvoiceContext>
                                                              <SubscriptionContext>
                                                                <Routes />
                                                              </SubscriptionContext>
                                                            </InvoiceContext>
                                                          </WhatsAppTemplateContext>
                                                        </LeadDistributionRulesProvider>
                                                      </LeadDataFieldProvider>
                                                    </TeamMemberContext>
                                                  </WaitingLoaderContext>
                                                </ModelContext>
                                              </DailogContext>
                                            </CampaignContext>
                                          </SocialMediaSetupContextProvider>
                                        </SidebarProvider>
                                      </AnalyticsProvider>
                                    </BusinessProvider>
                                  </LMSDashboardContext>
                                </LMSFilterContext>
                              </TabContext>
                            </LeadContext>
                          </ToastMessageContext>
                        </LoaderContext>
                      </BusinessProfileContext>
                    </SocketProvider>
                  </LoggedUserContext>
                </AuthContext>
              </AppProvider>
            </div>
          </ErrorBoundary>
        </BrowserRouter>
      </QueryClientProvider>
    </>
  );
}

const mapStateToProps = (state) => {
  return {
    loading: state.main.loading,
  };
};

export default connect(mapStateToProps, null)(App);
