import * as actions from "../actions/actionTypes";

const initialState = {
  selectedBusiness: null,
  allBusiness: [],
  businessOrders: [],
};

const getAllUserBusiness = (state, action) => {
  return {
    ...state,
    allBusiness: action.allBusiness,
  };
};

const getDefaultBusiness = (state, action) => {
  return {
    ...state,
    selectedBusiness: action.defaultBusiness,
  };
};

const storeBusinessOrders = (state, action) => {
  return {
    ...state,
    businessOrders: action.businessOrders,
  };
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.GET_ALL_USER_BUSINESS:
      return getAllUserBusiness(state, action);
    case actions.GET_DEFAULT_BUSINESS:
      return getDefaultBusiness(state, action);
    case actions.STORE_BUSINESS_ORDERS:
      return storeBusinessOrders(state, action);
    default:
      return state;
  }
};

export default reducer;
