/** @format */

import * as actions from "../actions/actionTypes";
import { updateObject } from "../../Shared/Utility";

const initialState = {
  token: localStorage.getItem("token"),
  userId: localStorage.getItem("userId"),
  loading: false,
  error: null,
  isSignup: true,
  collectionId: null,
  isLoggedIn:
    localStorage.getItem("token") && localStorage.getItem("userId")
      ? true
      : false,
  currentCount: 0,
  gst: 1.18,
  discounted_amount: null,
  discount: null,
  couponCode: null,
  couponDetail: {
    error: null,
    discounted_amount: null,
    discount: null,
    promoCode: null,
  },
  authRedirectPath: "/choose-plans",
  defaultRedirectPath: "/engage/chat",
};

const authStart = (state, action) => {
  return updateObject(state, { error: null, loading: true });
};

const authFail = (state, action) => {
  return updateObject(state, {
    error: action.error,
    loading: false,
  });
};

const authState = (state, action) => {
  return updateObject(state, { isLoggedIn: action.isLoggedIn });
};

export const authSuccess = (state, action) => {
  return updateObject(state, {
    token: action.token,
    userId: action.userId,
    error: null,
    isLoggedIn: true,
  });
};

export const authSteps = (state, action) => {
  return updateObject(state, {
    currentCount: action.currentCount,
  });
};

export const getUserCollectionId = (state, action) => {
  return updateObject(state, {
    collectionId: action.collectionId,
    loading: false,
  });
};

export const authLogout = (state, action) => {
  return updateObject(state, { token: null, userId: null, isLoggedIn: false });
};

export const applyCoupon = (state, action) => {
  return updateObject(state, {
    couponDetail: action.couponDetail,
  });
};

const setAuthRedirectPath = (state, action) => {
  return updateObject(state, { authRedirectPath: action.path });
};

const setDefaultRedirectPath = (state, action) => {
  return updateObject(state, { defaultRedirectPath: action.path });
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.AUTH_START:
      return authStart(state, action);
    case actions.AUTH_FAIL:
      return authFail(state, action);
    case actions.AUTH_SUCCESS:
      return authSuccess(state, action);
    case action.GET_USER_COLLECTION_ID:
      return getUserCollectionId(state, action);
    case actions.AUTH_STATE:
      return authState(state, action);
    case actions.AUTH_STEPS:
      return authSteps(state, action);
    case actions.AUTH_LOGOUT:
      return authLogout(state, action);
    case actions.APPLY_COUPON:
      return applyCoupon(state, action);
    case actions.SET_AUTH_REDIRECT_PATH:
      return setAuthRedirectPath(state, action);
    case actions.SET_DEFAULT_REDIRECT_PATH:
      return setDefaultRedirectPath(state, action);

    default:
      return state;
  }
};

export default reducer;
