import * as actions from "../actions/ActionTypes/lead";

const initialState = {
  notes: [],
  leadId: null,
};

const getLeadNotes = (state, action) => {
  console.log(state);
  return {
    ...state,
    notes: [...action.notes, ...state.notes],
  };
};

const clearLeadNotes = (state, action) => {
  return {
    ...state,
    notes: [],
  };
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.GET_LEAD_NOTES:
      return getLeadNotes(state, action);
    case actions.CLEAR_LEAD_NOTES:
      return clearLeadNotes(state, action);
    default:
      return state;
  }
};

export default reducer;
