import React, { useContext } from "react";
import { connect } from "react-redux";
import * as actions from "../Store/actions/index";

export const GlobalLoaderContext = React.createContext();

export const useLoader = () => {
  return useContext(GlobalLoaderContext);
};

const LoaderContext = ({
  startLoading,
  cancelLoading,
  error,
  children,
  loading,
}) => {
  const startLoader = () => {
    return startLoading();
  };

  const cancelLoader = () => {
    return cancelLoading();
  };

  const errorHandler = (err) => {
    return error(err);
  };

  const value = {
    startLoader,
    cancelLoader,
    errorHandler,
    isLoading: loading,
  };

  return (
    <GlobalLoaderContext.Provider value={value}>
      {children}
    </GlobalLoaderContext.Provider>
  );
};
const mapDispatchToProps = (dispatch) => {
  return {
    startLoading: () => dispatch(actions.startLoading()),
    cancelLoading: () => dispatch(actions.cancelLoading()),
    error: (err) => dispatch(actions.error(err)),
  };
};

const mapStateToProps = (state) => {
  return {
    loading: state.main.loading,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LoaderContext);
