export const getAllOrders = async () => {
  try {
    const data = await fetch(`${API_V2}/order/all-orders`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return await data.json();
  } catch (err) {
    return {
      message: err.message,
    };
  }
};
