/** @format */

import * as actions from "./actionTypes";

export const startLoading = () => {
  return {
    type: actions.START_LOADING,
  };
};

export const cancelLoading = () => {
  return {
    type: actions.CANCEL_LOADING,
  };
};

export const error = (error) => {
  return {
    type: actions.ERROR,
    error: error,
  };
};
