/** @format */

import * as actions from "../actions/actionTypes";

const initialState = {
  photo: [],
};

const getPhotos = (state, action) => {
  return {
    ...state,
    photo: action.photos,
  };
};

const addPhoto = (state, action) => {
  const photos = [...state.photo, action.data];
  return {
    ...state,
    photo: photos,
  };
};

const deletePhoto = (state = initialState, action) => {
  return {
    ...state,
    photo: state.photo.filter((el) => el.id !== action.id),
  };
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.GET_PHOTOS:
      return getPhotos(state, action);
    case actions.ADD_PHOTO:
      return addPhoto(state, action);
    case actions.DELETE_PHOTO:
      return deletePhoto(state, action);
    default:
      return state;
  }
};

export default reducer;
