import React, { useContext, useState } from "react";

export const Dashboard = React.createContext();

export const useLMSDashboard = () => useContext(Dashboard);

const LMSDashboardContext = ({ children }) => {
  const [isOpenUserProfile, openUserProfile] = useState(false);

  const [isMobileChatOpen, openMobileChat] = useState(false);

  const [modals, setModals] = useState({ openAddEnquiryModal: false });

  const handleLMSDashboardModal = (key) => {
    return setModals((prevState) => ({ ...modals, [key]: !prevState[key] }));
  };

  const value = {
    isOpenUserProfile,
    openUserProfile,
    isMobileChatOpen,
    openMobileChat,
    handleLMSDashboardModal,
    modals,
  };

  return <Dashboard.Provider value={value}>{children}</Dashboard.Provider>;
};

export default LMSDashboardContext;
