import axios from "axios";
import { AuthenticatedHeaders } from "utils/ApiHeaders";
import { API, API_V2 } from "../../Backend";
import decryptResponse from "../../Shared/decryptResponse";

let cancelToken;

export const fetchTableDataGet = async ({ query, data }) => {
  try {
    if (typeof cancelToken != typeof undefined) {
      cancelToken.cancel("Operation canceled due to new request.");
    }

    let source = axios.CancelToken.source();

    cancelToken = source;

    const res = await axios(`${API_V2}/honda-dashboard/${query}`, {
      method: "POST",
      headers: AuthenticatedHeaders(),
      data: data,
      cancelToken: source.token,
    });

    return res.data;
  } catch (err) {
    return { message: err.message };
  }
};

