export const fetchRegionData = async () => {
  try {
    const res = await axios(`${API}/lead-dashboard/get-regions`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-BusinessId": localStorage.getItem("businessId"),
      },
    });

    return decryptResponse({ data: res.data });
  } catch (err) {
    return { error: err.message };
  }
};

export const FetchUAWOLLeadsData = async (filterDate) => {
  try {
    const res = await axios({
      url: `${API_V2}/honda-dashboard/unassigned-non-location-leads`,
      method: "POST",
      headers: AuthenticatedHeaders(),
      data: filterDate,
    });

    const { data } = res;

    return data;
  } catch (error) {
    return { message: error.message };
  }
};

export const FetchMonthlyLeadsData = async (data) => {
  try {
    const res = await fetch(
      `${API_V2}/honda-dashboard/download-monthly-leads`,
      {
        method: "POST",
        headers: AuthenticatedHeaders(),
        body: JSON.stringify(data),
      }
    );
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

