/** @format */

import React from "react";
import "./Input.css";
import attach from "../../../assets/icons/attach.svg";

const Input = (props) => {
  return props.type !== "file" && props.type !== "select" ? (
    <div style={{ position: "relative" }}>
      <input
        className="inputComponent"
        type={props.type}
        name={props.name}
        defaultValue={props.value}
        onChange={(e) => {
          props.inputChangedHandler && props.inputChangedHandler(e, props.id);
          if (e.target.value.length > props.maxLength) {
            e.target.value = e.target.value.slice(0, props.maxLength);
          }
        }}
        minLength={props.minLength}
        readOnly={props.readOnly}
        required={props.required}
        onKeyDown={(e) => props.onKeyPressHandler && props.onKeyPressHandler(e)}
        autoComplete="true"
        onInput={(e) => {
          if (e.target.value > props.maxLength) {
            e.target.value = e.target.value.slice(0, props.maxLength);
          }
        }}
      />
      <label
        htmlFor={props.name}
        onClick={(e) => e.stopPropagation()}
        className="labelComponent"
      >
        <span>{props.placeholder}</span>
      </label>
    </div>
  ) : props.type === "file" ? (
    <div
      style={{
        display: "flex",
        justifyContent: "space-between",
      }}
      className={props.className}
    >
      {props.value !== null && props.value?.length !== 0 && (
        <div
          style={{
            backgroundColor: "#F2F3F7",
          }}
        >
          <img
            src={
              props.value && typeof props.value !== "string"
                ? URL.createObjectURL(props.value[0])
                : props.value
            }
            alt="attach"
            style={{ width: "100%", maxWidth: "112px" }}
          />
        </div>
      )}
      <div
        style={{
          alignItems: "center",
          backgroundColor: "#F2F3F7",
          borderRadius: "15px",
          display: "flex",
          justifyContent: "center",
          flexDirection: "column",
          position: "relative",
          height: "112px",
          textAlign: "center",
          width: "112px",
        }}
      >
        <span className="icon">
          <img src={attach} alt="attach" />
        </span>
        <h4
          style={{
            color: "#616C7D",
            fontWeight: 500,
            fontSize: "15px",
            lineHeight: "18px",
          }}
        >
          {props?.title ?? props?.placeholder}
        </h4>
        <p
          style={{
            color: "#758399",
            fontWeight: 400,
            fontSize: "10px",
            lineHeight: "12px",
            marginBottom: 0,
          }}
        >
          {"png or jpg format"}
        </p>
        <input
          defaultValue={props.value && props.value}
          className="inputComponent"
          accept={"image/png, image/jpeg"}
          type={props.type}
          name={props.name}
          onChange={(e) => {
            props.inputChangedHandler(e, props.id);
          }}
          // required={props.required}
          onKeyDown={(e) =>
            props.onKeyPressHandler && props.onKeyPressHandler(e)
          }
        />
        {/* {props.value !== null && props.value?.length !== 0 && (
          // eslint-disable-next-line jsx-a11y/no-distracting-elements
          <marquee>{props.value[0]?.name}</marquee>
        )} */}
      </div>
    </div>
  ) : props.type === "select" ? (
    <div style={{ position: "relative" }}>
      <input
        className="inputComponent"
        type={props.type}
        name={props.name}
        value={props.value}
        readOnly={props.readOnly}
        onChange={(e) => {
          props.inputChangedHandler(e, props.id);
          if (props.countryList) {
            document.querySelector(".country").classList.remove("hide");
          } else {
            document.querySelector(".dropdown").classList.remove("hide");
          }
        }}
        required={props.required}
        onKeyDown={(e) => props.onKeyPressHandler && props.onKeyPressHandler(e)}
        autoComplete="false"
        onInput={(e) => {
          if (e.target.value > props.maxLength)
            e.target.value = e.target.value.slice(0, props.maxLength);
        }}
      />
      <label
        htmlFor={props.name}
        onClick={(e) => e.stopPropagation()}
        className="labelComponent"
      >
        <span>{props.placeholder}</span>
      </label>

      <div className={"dropdown hide"}>
        <ul className="dropDown-content">
          {props.list &&
            props.list.map((list, i) => {
              if (
                list.label.toLowerCase().includes(props.value.toLowerCase())
              ) {
                return (
                  <>
                    <li
                      onClick={(e) => {
                        document
                          .querySelector(".dropdown")
                          .classList.add("hide");

                        props.selectListValue(e, props.id, i);
                      }}
                      key={list.label}
                    >
                      {list.label}
                    </li>
                  </>
                );
              }

              return <></>;
            })}

          {/* {props.countryList &&
            props.countryList.map((country) => {
              // console.log(country);
              if (
                country.country
                  .toLowerCase()
                  .includes(props.value.toLowerCase())
              ) {
                return (
                  <>
                    <li
                      onClick={(e) => {
                        document
                          .querySelector(".dropdown")
                          .classList.add("hide");

                        props.selectListValue(props.id, country.index);
                      }}
                      key={country.index}
                    >
                      {country.country}
                    </li>
                  </>
                );
              }
            })} */}

          {props.stateList !== undefined &&
            props.stateList.map((state) => {
              // console.log(country);
              if (
                state.state.toLowerCase().includes(props.value.toLowerCase())
              ) {
                return (
                  <>
                    <li
                      onClick={(e) => {
                        console.log(document.querySelector(".dropdown"));
                        document
                          .querySelector(".dropdown")
                          .classList.add("hide");

                        props.selectListValue(props.id, state.index);
                      }}
                      key={state.index}
                    >
                      {state.state}
                    </li>
                  </>
                );
              }
            })}
        </ul>
      </div>
    </div>
  ) : null;
};

export default Input;
