/** @format */

import React from "react";
import classes from "./Profile.module.css";

import CheckCircleOutlineIcon from "@material-ui/icons/CheckCircleOutline";

const Profile = (props) => {
  return (
    <div className={classes.Page_Profile}>
      <label className={classes.Page_label} htmlFor={props.id}>
        <div className={classes.imgContainer}>
          <img className={classes.PageImg} src={props.img} alt={props.alt} />
        </div>
        <div>
          <h1>{props.name}</h1>
          {props.used === "yes" && (
            <p className={classes.alreadyUsed}>Page already in use</p>
          )}
        </div>
      </label>

      {props.used === "yes" && (
        <CheckCircleOutlineIcon color="primary" style={{ color: "#12B0EE" }} />
      )}

      {props.used === "no" && (
        <input
          className={classes.Page_Radio}
          type="radio"
          name={props.tag}
          id={props.id}
          onClick={props.onClick}
          value={props.value}
          required
        />
      )}
    </div>
  );
};

export default Profile;
