import React, { useContext, useState } from "react";
import { withRouter } from "react-router-dom";
import { GetLeadDataFieldsForFilter } from "../../api/LMSDashboard/LMSFilter";
import { useToastMessage } from "../ToastMessageContext";
import { useLead } from "./LeadContext";
import queryString from "query-string";
import { useTab } from "./TabContext";

const GlobalLMSFilterContext = React.createContext();

export const useLMSFilter = () => {
  return useContext(GlobalLMSFilterContext);
};

const LMSFilterContext = ({ children, history, location }) => {
  const { setErrorMessage } = useToastMessage();
  const toastMessage = useToastMessage();

  const {
    getLMSLeads,
    resetLMSPageCount,
    clearAllLeads,
    clearLeads,
    page,
    resetLMSLeadCount,
  } = useLead();

  const { currentTabName } = useTab();

  const [leadDataFields, setLeadDataFields] = useState([]);

  const [appliedFilters, setAppliedFilters] = useState(0);

  const [searchQuery, setSearchQuery] = useState({});

  const getAppliedFilters = async (parameter) => {
    const filters =
      parameter.length >= 1 &&
      parameter.filter(
        (e) =>
          (e.value && e.value.length > 0) ||
          (e.type === "date" && e.value.from && e.value.to)
      ).length;

    setAppliedFilters(filters);
  };

  const fetchFilterFromURL = (leadDataFields) => {
    if (location.search.length > 0) {
      const query = queryString.parse(location.search);

      leadDataFields.forEach((e) => {
        Object.entries(query).findIndex((r) => {
          if (e.field_name === "date") {
            if (r[0] === "from") {
              e.value = { ...e.value, from: r[1] };
            }

            if (r[0] === "to") {
              e.value = { ...e.value, to: r[1] };
            }
          }

          if (e.field_name === r[0]) {
            e.value = r[1];
          }

          return -1;
        });
      });
    }

    getAppliedFilters(leadDataFields);

    return setLeadDataFields(leadDataFields);
  };

  const fetchLeadDataFieldsForFilter = async () => {
    try {
      const res = await GetLeadDataFieldsForFilter();

      if (!res.success) return setErrorMessage(res.message);

      const arr = [...leadDataFields, ...res.payload];

      arr.forEach((e) => (e.value = ""));

      return fetchFilterFromURL(arr);
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const parameterValue = (key) => {
    return leadDataFields[key].value;
  };

  // Handle Parameter value
  const handleParameterValue = (key, value, type, dateType) => {
    const parameterArr = [...leadDataFields];

    if (type === "date") {
      parameterArr[key] = {
        ...parameterArr[key],
        value: {
          ...parameterArr[key].value,
          [dateType]: value,
        },
      };

      return setLeadDataFields(parameterArr);
    }

    parameterArr[key] = {
      ...parameterArr[key],
      value: value,
    };

    return setLeadDataFields(parameterArr);
  };

  // Apply Filter
  const applyFilter = () => {
    let s = {};
    let query = {};

    leadDataFields.forEach((e, i) => {
      if (e.field_type === "date") {
        if (e.value.from && e.value.to) {
          s[e.field_name] = { from: e.value.from, to: e.value.to };
          query.from = e.value.from;
          query.to = e.value.to;
        }
      }
      if (e.field_type !== "date" && e.value.length > 0) {
        s[e.field_name] = e.value;
        query[e.field_name] = e.value;
      }
    });

    const queryUrl = queryString.stringify(query);

    clearLeads();
    clearAllLeads();
    resetLMSPageCount();
    resetLMSLeadCount();

    getAppliedFilters(leadDataFields);

    getLMSLeads(
      1,
      currentTabName,
      { filter: { ...s, ...searchQuery } },
      toastMessage
    );

    return history.push(`${location.pathname}?${queryUrl}${location.hash}`);
  };

  const setFiltersFromURL = () => {
    let s = {};

    leadDataFields.forEach((e, i) => {
      if (e.value.length > 0) {
        s[e.field_name] = e.value;
      }
    });

    // const queryUrl = queryString.stringify(s);

    clearLeads();
    clearAllLeads();
    resetLMSPageCount();

    getLMSLeads(
      page[currentTabName] || 1,
      currentTabName,
      { filter: { ...s, ...searchQuery } },
      toastMessage
    );
  };

  const clearFilters = () => {
    leadDataFields.forEach((e) => (e.value = ""));

    setLeadDataFields(leadDataFields);

    clearLeads();
    clearAllLeads();
    resetLMSPageCount();
    resetLMSLeadCount();
    setSearchQuery({});

    getAppliedFilters(leadDataFields);

    getLMSLeads(1, currentTabName, { filter: {} }, toastMessage);

    return history.push(`${location.pathname}${location.hash}`);
  };

  const clearAppliedFilters = () => {
    leadDataFields.forEach((e) => (e.value = ""));

    setLeadDataFields(leadDataFields);

    clearLeads();
    clearAllLeads();
    resetLMSPageCount();
    resetLMSLeadCount();

    getAppliedFilters(leadDataFields);

    return history.push(`${location.pathname}${location.hash}`);
  };

  const value = {
    handleParameterValue,
    parameterValue,
    applyFilter,
    fetchFilterFromURL,
    setFiltersFromURL,
    clearFilters,
    appliedFilters,
    searchQuery,
    setSearchQuery,
    clearAppliedFilters,
    fetchLeadDataFieldsForFilter,
    leadDataFields,
  };

  return (
    <GlobalLMSFilterContext.Provider value={value}>
      {children}
    </GlobalLMSFilterContext.Provider>
  );
};

export default withRouter(LMSFilterContext);
