import { API, API_V2 } from "../../Backend";
import { AuthenticatedHeaders } from "../../utils/ApiHeaders";

export const fetchLeadNotes = async ({ leadId }) => {
  try {
    const res = await fetch(`${API_V2}/lead-notes/all/${leadId}`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const fetchTurnOffReminder = async ({ leadId }) => {
  try {
    const res = await fetch(`${API}/lead/reminder-off?leadId=${leadId}`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "Content-Type": "application/json",
        "X-BusinessId": localStorage.getItem("businessId"),
      },
    });
    return await res.json();
  } catch (err) {
    return { error: err.message };
  }
};
