export const updateStatusById = async (data, leadId) => {
  try {
    const res = await fetch(`${API_V2}/leads/status/${leadId}`, {
      method: "PUT",
      headers: AuthenticatedHeaders(),
      body: JSON.stringify(data),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

