import * as actions from "../actions/actionTypes";

const initialState = {
  data: null,
  error: null,
  loading: false,
  test: null,
};

const getData = (state, action) => {
  return {
    ...state,
    loading: false,
    data: action.data,
  };
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.GET_DATA:
      return getData(state, action);
    default:
      return state;
  }
};

export default reducer;
