/** @format */

import { AuthenticatedHeaders } from "utils/ApiHeaders";
import { API, API_V2 } from "../../Backend";

import decryptResponse from "../../Shared/decryptResponse";
import encryptResponse from "../../Shared/encryptResponse";

export const createBusiness = async (data) => {
  try {
    const res = await fetch(`${API}/business/create`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "Content-Type": "application/json",
      },
      body: JSON.stringify(encryptResponse(data)),
    });
    return await res.json();
  } catch (err) {
    return console.log(err);
  }
};

