import { applyMiddleware, createStore, combineReducers, compose } from "redux";
import thunk from "redux-thunk";

import uploadReducer from "./reducer/UploadSection";
import canvasReducer from "./reducer/canvas";
import fontReducer from "./reducer/Font";
import ImageHandler from "./reducer/ImageHandler";
import authReducer from "./reducer/auth";
import mainReducer from "./reducer/main";
import businessReducer from "./reducer/business";
import userReducer from "./reducer/user";
import inviteReducer from "./reducer/invite";
import LMSDashboardReducer from "./reducer/LMSDashboard";
import departmentReducer from "./reducer/department";
import leadReducer from "./reducer/lead";

import strategyReducer from "./calendarDashboard/strategy/strategySlice";
import postReducer from "./calendarDashboard/posts/postSlice";
import eventReducer from "./calendarDashboard/events/eventSlice";

import leadProfileReducer from "./LMSDashboard/LeadProfile/reducer";

// Custom Login
import customLoginReducer from "./CustomLogin/reducer";

const composeEnhancers =
  typeof window === "object" && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({
        // Specify extension’s options like name, actionsBlacklist, actionsCreators, serialize...
      })
    : compose;

const rootReducer = combineReducers({
  upload: uploadReducer,
  canvas: canvasReducer,
  font: fontReducer,
  imageHandler: ImageHandler,
  auth: authReducer,
  main: mainReducer,
  business: businessReducer,
  user: userReducer,
  invite: inviteReducer,
  LMSDashboard: LMSDashboardReducer,
  department: departmentReducer,
  lead: leadReducer,
  strategyState: strategyReducer,
  postState: postReducer,
  eventsState: eventReducer,
  customLoginState: customLoginReducer,
  LeadProfileState: leadProfileReducer,
});

const store = createStore(
  rootReducer,
  composeEnhancers(applyMiddleware(thunk))
);

export default store;
