import React, { useContext, useState } from "react";

export const Campaign = React.createContext();

export const useCampaign = () => useContext(Campaign);

const CampaignContext = ({ children }) => {
  const [createNew, toggleCreateNew] = useState(false);
  const [selectedCampaignId, setSelectedCampaignId] = useState("");
  const [selectedCampaignProgress, setSelectedCampaignProgress] = useState("");
  const [searchQuery, setSearchQuery] = useState("");
  const [campaignDescription, setCampaignDescription] = useState("");
  const [campaignType, setCampaignType] = useState("");
  const [load, setLoad] = useState(false);

  const [selectedCampaignName, setSelectedCampaignName] = useState(null);

  const value = {
    createNew,
    toggleCreateNew,
    selectedCampaignId,
    setSelectedCampaignId,
    selectedCampaignProgress,
    setSelectedCampaignProgress,
    load,
    setLoad,
    searchQuery,
    setSearchQuery,
    campaignDescription,
    selectedCampaignName,
    setSelectedCampaignName,
    setCampaignDescription,
    campaignType,
    setCampaignType,
  };

  return <Campaign.Provider value={value}>{children}</Campaign.Provider>;
};

export default CampaignContext;
