import React from "react";
import classes from "./Backdrop.module.css";

const backdrop = ({ zIndex, clicked, show, style, ...props }) =>
  show ? (
    <div
      style={{ zIndex: zIndex || 111, ...style }}
      onClick={clicked}
      className={classes.Backdrop}
      {...props}
    ></div>
  ) : null;

export default backdrop;
