import React, { FC } from "react";
import { Button, ButtonProps } from "@material-ui/core";
import css from "./index.module.css";

interface MuiCustomButtonProps {
  showLoader?: boolean;
  applyThemeStyles?: boolean;
  spinnerType?: "spinner" | "circle-o-notch" | "refresh";
  classes?: any;
}

const MuiCustomButton: FC<MuiCustomButtonProps & ButtonProps> = ({
  classes,
  showLoader = false,
  applyThemeStyles = false,
  spinnerType = "spinner",
  ...props
}) => {
  // We can inject some CSS into the DOM.
  return (
    <Button
      type="submit"
      classes={{
        root: applyThemeStyles ? css.ThemeStyle : css.Button,
        ...classes,
      }}
      {...props}
    >
      {showLoader && (
        <i
          style={{ marginRight: "8px" }}
          className={`fa fa-${spinnerType} fa-spin`}
        ></i>
      )}

      {props.children}
    </Button>
  );
};

export default MuiCustomButton;
