import { createSlice, current } from "@reduxjs/toolkit";

const loginSlice = createSlice({
  name: "intersect",
  initialState: {
    error: false,
    isLoading: false,
    isLoggedIn: false,
    token: null,
    userId: null,
    errorMessage: "",
  },
  reducers: {
    storeToken: (state, action) => {
      const { userId, token } = action.payload;

      return {
        ...current(state),
        token: token,
        userId: userId,
      };
    },
    errorHandler: (state, action) => {
      const { error, errorMessage } = action.payload;

      return {
        ...current(state),
        error,
        errorMessage,
      };
    },
  },
});

export const { storeToken, errorHandler } = loginSlice.actions;

export default loginSlice.reducer;
