import { updateStatusById, updateLeadById } from "../../api/Lead";
import { storeLeadNotes } from "../LMSDashboard/LeadProfile/reducer";
import { GET_FULL_LEAD_DATA, UPDATE_LEAD } from "./actionTypes";
import { CLEAR_LEAD_NOTES, GET_LEAD_NOTES } from "./ActionTypes/lead";

export const addLeadNotes = ({ notes }) => {
  return {
    type: GET_LEAD_NOTES,
    notes: notes,
  };
};

