export const clearLeadNotes = () => {
  return {
    type: CLEAR_LEAD_NOTES,
  };
};

export const updateCurrentStatus = (data, leadId, toastMessage) => {
  return async (dispatch) => {
    try {
      const res = await updateStatusById(data, leadId);
      if (!res.success) return toastMessage.setErrorMessage(res.message);
      const notes = [res.payload];
      return dispatch(storeLeadNotes({ lead_notes: notes, leadId }));
    } catch (err) {
      return toastMessage.setErrorMessage(err.message);
    }
  };
};

export const updateLeadDetail = ({ data, toastMessage, leadId }) => {
  return async (dispatch) => {
    try {
      const res = await updateLeadById({ data: data.lead_data }, leadId);

      if (!res.success) return toastMessage.setErrorMessage(res.message);

      const notes = [res.payload];

      dispatch({
        type: GET_FULL_LEAD_DATA,
        currentLeadData: data,
        currentLeadId: leadId,
      });

      dispatch({ type: UPDATE_LEAD, lead: data, leadId });

      dispatch(storeLeadNotes({ lead_notes: notes, leadId }));

      return toastMessage.setSuccessMessage(
        "Successfully updated lead detail!"
      );
    } catch (err) {
      return toastMessage.setErrorMessage(err.message);
    }
  };
};

export const updateLead = ({ toastMessage, data, leadId }) => {
  return async (dispatch) => {
    try {
      return dispatch({ type: UPDATE_LEAD, lead: data, leadId });
    } catch (err) {
      return toastMessage.setErrorMessage(err.message);
    }
  };
};
