/** @format */

import { authSteps } from "../auth/auth";
import { cancelLoading, error, startLoading } from "../main";

const {
  getLongLivedToken,
  saveSelectedFBPage,
  storeInstagramProfile,
  getLongLivedTokenConnect,
} = require("../../../api/Auth");

export const getFacebookLongLivedToken = (token, email, businessId) => {
  return (dispatch) => {
    dispatch(startLoading());
    getLongLivedToken(
      {
        token: token,
        email: email,
      },
      businessId
    )
      .then((res) => {
        dispatch(authSteps(11));
        dispatch(cancelLoading());
      })
      .catch((err) => {
        dispatch(error(err));
      });
  };
};
export const getFacebookLongLivedTokenConnect = (token, email, businessId) => {
  return (dispatch) => {
    dispatch(startLoading());
    getLongLivedTokenConnect(
      {
        token: token,
        email: email,
      },
      businessId
    )
      .then((res) => {
        console.log(res);
        dispatch(cancelLoading());
        this.props.history.push("/dashboard");
      })
      .catch((err) => {
        dispatch(error(err));
      });
  };
};

export const storeSelectedFBPage = (businessId, page_id) => {
  return (dispatch) => {
    dispatch(startLoading());
    saveSelectedFBPage(businessId, {
      page_id: page_id,
    })
      .then((data) => {
        dispatch(authSteps(12));
        dispatch(cancelLoading());
      })
      .catch((err) => {
        dispatch(error(err));
      });
  };
};

export const storeInstagramData = (businessId, data) => {
  return (dispatch) => {
    dispatch(startLoading());
    storeInstagramProfile(businessId, data)
      .then(() => {
        dispatch(authSteps(13));
        dispatch(cancelLoading());
      })
      .catch((err) => {
        dispatch(error(err));
      });
  };
};
