import {
  createContext,
  useCallback,
  useContext,
  useEffect,
  useState,
} from "react";
import { v4 as uuid } from "uuid";

import Permissions from "api/permissions";

export const AppContext = createContext();

export const useApp = () => useContext(AppContext);

const AppProvider = ({ children }) => {
  const [deviceSession, setDeviceSession] = useState(null);
  const [isOpenDashboardBackdrop, openDashboardBackdrop] = useState(false);
  const [permitSuccess, setPermitSuccess] = useState(false);
  const [permission, setPermission] = useState(undefined);
  const [refetchPermission, doRefetchPermission] = useState(Date.now());
  const fetchPermissions = useCallback(async () => {
    try {
      const res = await Permissions.getBusinessSettingsPermit();
      if (!res.success) {
        throw Error(res.message);
      }
      setPermitSuccess(res.success);
      setPermission(res.payload?.business_settings);
    } catch (e) {
      console.error(e);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [refetchPermission]);

  useEffect(() => {
    fetchPermissions();
  }, [fetchPermissions]);

  useEffect(() => {
    setDeviceSession(uuid());

    sessionStorage.setItem("loadedChatFiles", JSON.stringify([]));
  }, []);

  const [themeColors] = useState({ primary: "#12B0EE" });

  return (
    <AppContext.Provider
      value={{
        deviceSession,
        isOpenDashboardBackdrop,
        openDashboardBackdrop,
        themeColors,
        permitSuccess,
        permission,
        doRefetchPermission,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};

export default AppProvider;
