import { AuthenticatedHeaders } from "utils/ApiHeaders";
import { API, API_V2, TESTAPI } from "../../Backend";

import { db } from "../../firebase";

import decryptResponse from "../../Shared/decryptResponse";
import encryptResponse from "../../Shared/encryptResponse";

export const login = async (field, password) => {
  try {
    const res = await fetch(`${API_V2}/auth/sign-in`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        ...(parseInt(field)
          ? {
              mobile_number:
                field.charAt(0) === "+"
                  ? field.replace(/-|\s/g, "")
                  : `+${field.replace(/-|\s/g, "")}`,
            }
          : { email: field }),
        password,
      }),
    });
    //const result =
    const result = await res.json();
    const user = await fetch(`${API_V2}/user`, {
      method: "GET",
      // credentials: true,
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${result.payload.token}`,
      },
    });
    const userData = await user.json();
    return { ...result.payload, user: userData.payload };
  } catch (err) {
    return { message: err.message };
  }
};
export const resetPassword = async (data) => {
  try {
    const res = await fetch(`${API_V2}/user/update-password`, {
      method: "PUT",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `bearer ${localStorage.getItem("token")}`,
      },
      body: JSON.stringify(data),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

