export const getPlans = () => {
  const plans = db.collection("plans");

  plans.get().then((plan) => {
    plan.forEach((plan) => {
      return plan.data();
    });
  });
};

// Get facebook Long Lived Token
export const getLongLivedToken = async (token, businessId) => {
  try {
    const res = await fetch(`${API}/facebook/long-lived/${businessId}`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
      },
      body: JSON.stringify(encryptResponse(token)),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

