import Axios from "axios";
import { API_V2 } from "../../../Backend";
import { getLeadById, saveLMSLead } from "../../../api/LMSDashboard";
import * as actions from "../actionTypes";
import { clearLeadNotes } from "../lead";
import { cancelLoading, error, startLoading } from "../main";
import { getLeadNotes } from "../../LMSDashboard/LeadProfile/actions";
import { AuthenticatedHeaders } from "../../../utils/ApiHeaders";
import { DataArray } from "@mui/icons-material";

export const clearAllLeads = () => {
  return {
    type: actions.CLEAR_ALL_LEADS,
  };
};

export const clearLeads = () => {
  return {
    type: actions.CLEAR_LEADS,
  };
};

export const resetLMSPageCount = () => {
  return {
    type: actions.RESET_LMS_PAGE_COUNT,
  };
};

export const resetLMSLeadCount = () => {
  return {
    type: actions.RESET_LMS_LEAD_COUNT,
  };
};

export const updateLMSLeadCount = (tabName) => {
  return {
    type: actions.UPDATE_LMS_LEAD_COUNT,
    tabName: tabName,
  };
};

export const getCurrentLeadId = (currentLeadId) => {
  return {
    type: actions.GET_CURRENT_LEAD_ID,
    currentLeadId: currentLeadId,
  };
};

export const setFullLeadLoader = (fullLeadLoader) => {
  return {
    type: actions.SET_FULL_LEAD_LOADER,
    fullLeadLoader: fullLeadLoader,
  };
};

export const getLMSLeads = (
  page,
  tab,
  { filter },
  toastMessage,
  cancelToken
) => {
  return async (dispatch) => {
    dispatch(cancelLoading());
    dispatch({ type: actions.START_LOADING_LMS });

    if (typeof cancelToken != typeof undefined) {
      cancelToken.cancel("Operation canceled due to new request.");
    }

    let source = Axios.CancelToken.source();

    dispatch({ type: actions.GET_CANCEL_TOKEN, cancelToken: source });

    Axios({
      method: "POST",
      url: `${API_V2}/leads/all?page=${page}&limit=50&tab=${tab}`,
      headers: AuthenticatedHeaders(),
      data: { filter },
      cancelToken: source.token,
    })
      .then((res) => {
        let data = res.data;

        const isFailed = !data.success;

        if (isFailed) {
          dispatch({
            type: actions.HAS_MORE,
            hasMore: false,
          });

          return dispatch({
            type: actions.ERROR_LMS,
            error: data.message,
          });
        }

        dispatch({
          type: actions.HAS_MORE,
          hasMore: data.payload.data.length > 0,
        });

        if (tab === "all") {
          dispatch({
            type: actions.GET_ALL_LMS_LEADS,
            leads: data.payload.data,
            tabName: tab,
            leadCount: data.payload.meta.total,
            page: data.payload.meta.current_page + 1,
          });
        } else {
          dispatch({
            type: actions.GET_LEADS,
            leads: data.payload.data,
            tabName: tab,
            leadCount: data.payload.meta.total,
            page: data.payload.meta.current_page + 1,
          });
        }

        return dispatch({ type: actions.CANCEL_LOADING_LMS });
      })
      .catch((err) => {
        if (Axios.isCancel(err)) return;
        dispatch({
          type: actions.HAS_MORE,
          hasMore: false,
        });
        toastMessage.setErrorMessage(err.message);
        dispatch(error(err.message));
        return dispatch({ type: actions.ERROR_LMS, error: err.message });
      });
    return () => cancelToken && cancelToken();
  };
};

export const getFullLeadData = (leadId, toastMessage, showLoader = true) => {
  return async (dispatch) => {
    // These clears all the notes
    dispatch(clearLeadNotes());

    dispatch(setFullLeadLoader(true));

    if (showLoader) dispatch(startLoading());

    try {
      const res = await getLeadById(leadId);
      if (!res.success) {
        dispatch(error(res.message));
        dispatch(setFullLeadLoader(false));

        return toastMessage.setErrorMessage(res.error);
      }

      dispatch(setFullLeadLoader(false));

      return dispatch({
        type: actions.GET_FULL_LEAD_DATA,
        currentLeadData: res.payload,
        currentLeadId: res.payload.id,
      });
    } catch (err) {
      toastMessage.setErrorMessage(err.message);
      dispatch(setFullLeadLoader(false));
    }
  };
};

export const storeLMSLead = (leadId, data, toastMessage) => {
  return async (dispatch) => {
    toastMessage.setDisabled();

    return saveLMSLead(leadId, data)
      .then((data) => {
        if (!data.success) return toastMessage.setErrorMessage(data.message);

        toastMessage.setSuccessMessage(data.message);

        dispatch(getLeadNotes({ leadId, toastMessage }));

        return dispatch({ type: "STORE_LEAD" });
      })
      .catch((err) => {
        dispatch(error(err.message));

        return toastMessage.setErrorMessage(err.message);
      });
  };
};

