import React, { useContext, useState } from "react";
import AnalyticsAPI from "../api/Analytics";
import { fetchIsDepartmentHead } from "../api/Auth";
import { useToastMessage } from "./ToastMessageContext";

export const AnalyticsContext = React.createContext();

export const useAnalytics = () => useContext(AnalyticsContext);

const AnalyticsProvider = ({ children }) => {
  const { setErrorMessage, setSuccessMessage } = useToastMessage();
  const [isOpenWorkspaceModal, openWorkspaceModal] = useState(false);

  const [workspaces, setWorkspaces] = useState(undefined);

  const [currentWorkspaceData, setCurrentWorkspaceData] = useState(null);
  const [currentWorkspace, setCurrentWorkspace] = useState(undefined);

  const [currentBusiness, setCurrentBusiness] = useState(undefined);
  const [leads, setLeads] = useState([]);
  const [workspaceData, setWorkspaceData] = useState(undefined);
  const [userRoleFilterData, setUserRoleFilterData] = useState([]);

  const [dataFields, setDataFields] = useState(null);
  const [shouldCached, setShouldCached] = useState(true);

  const [isLoading, setLoading] = useState(false);

  const [, setDepartment] = useState(false);
  const [, isDepartmentExist] = useState(false);

  const [drawers, setDrawers] = useState({
    openAddChartDrawer: false,
    openCreateLineChartDrawer: false,
    openCreatePieChartDrawer: false,
    openCreateDisplayFeed: false,
    openCreatePerformanceTableDrawer: false,
  });

  const [memberUserId, setMemberUserId] = useState(null);

  const [noteConfig, setNoteConfig] = useState({
    message: undefined,
  });

  const [state, setState] = useState({
    isVisible: true,
    dimmed: false,
    readyState: false,
    modalVisible: false,
    loader: false,
    businesses: undefined,
    editChart: false,
    editParams: undefined,
    showWorkSpaceModal: false,
    editInstance: false,
    isLayout: false,
    showMessageScreen: false,
  });

  const [userRole, setUserRole] = useState(null);

  const setNoteMessage = (text) => {
    setNoteConfig({ ...noteConfig, message: text });

    setState({ ...state, showMessageScreen: true });
  };

  const hideNoteMessage = () => {
    setNoteConfig({ ...noteConfig, message: undefined });

    setState({ ...state, showMessageScreen: false });
  };

  const startLoading = () => {
    return setLoading(true);
  };

  const cancelLoading = () => {
    return setLoading(false);
  };

  const editChart = (chart) => {
    return setState({
      ...state,
      editChart: true,
      editParams: chart,
    });
  };

  const getLeadDataFields = async () => {
    try {
      const res = await new AnalyticsAPI().fetchLeadDataFields();

      if (res.error) {
        return setErrorMessage(res.error);
      }

      setDataFields(res.data_fields);
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const toggleWorkspaceModal = () => {
    setState((prevState) => ({
      ...prevState,
      showWorkSpaceModal: !prevState.showWorkSpaceModal,
    }));
  };

  const closeModal = () => {
    setState((prevState) => ({
      ...prevState,
      showWorkSpaceModal: !prevState.showWorkSpaceModal,
    }));
  };

  const getWorkspaces = async (bid, query) => {
    startLoading();

    new AnalyticsAPI()
      .fetchDashboards(bid, query)
      .then((res) => {
        setWorkspaces(res.data);
        setUserRole(res.role);
        cancelLoading();
      })
      .catch((err) => {
        setUserRole(null);
        setErrorMessage(err.message);
        return cancelLoading();
      });
  };

  const getWorkspaceById = async ({ id, role }) => {
    try {
      const res = await new AnalyticsAPI().getDashboardByIdFetch(
        id,
        `?role=${role || userRole || ""}`
      );

      if (res.error) {
        setCurrentWorkspace(undefined);
        return setErrorMessage(res.error);
      }

      hideNoteMessage();

      return setCurrentWorkspaceData(res.data);
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const getWorkspaceData = async (bid, did, cache, role, idf) => {
    try {
      if (role !== "admin" && (!idf || idf.length <= 1)) {
        return cancelLoading();
      }

      startLoading();

      const res = await new AnalyticsAPI().fetchDashboardData(
        bid,
        did,
        cache,
        role,
        userRole,
        idf
      );

      if (res.error) {
        if (!res.chart) {
          hideNoteMessage();
          setState({ ...state, noChart: true });
        } else {
          setState({ ...state, noChart: false });
          setNoteMessage("Please select user from dropdown list.");
        }

        return cancelLoading();
      }

      setState({ ...state, noChart: false });

      setWorkspaceData(res.data);

      hideNoteMessage();

      return cancelLoading();
    } catch (err) {
      setErrorMessage(err.message);

      setNoteMessage("Something Went Wrong!");

      return cancelLoading();
    }
  };

  const getBusiness = (bid) => {
    getWorkspaces(bid);
    setCurrentBusiness(bid);
  };

  const _currentBusiness = (id, allBusiness) => {
    if (!id || !allBusiness.length <= 0) {
      return undefined;
    }
    return allBusiness.find((el, idx) => {
      return el._id === id;
    });
  };

  const createChartService = async (data, role, cb) => {
    try {
      startLoading();

      setShouldCached(false);

      const res = await new AnalyticsAPI().createChart(data);

      if (res.error) {
        return setErrorMessage(res.error);
      }

      setSuccessMessage("Chart created");

      setState({ ...state, modalVisible: false });

      const id = res.workspaceId;
      // const layout = res.layout;

      if (currentWorkspaceData._id !== id) {
        return console.log(id, currentWorkspaceData._id);
      }

      getWorkspaceById({ id: currentWorkspace });

      await getWorkspaceData(
        currentBusiness,
        currentWorkspace,
        false,
        role,
        role !== "admin" ? memberUserId : ""
      );

      return setShouldCached(true);
    } catch (err) {
      setState({ ...state, modalVisible: false });

      cancelLoading();

      return setErrorMessage(err.message);
    }
  };

  const editChartService = (data, role) => {
    startLoading();

    setShouldCached(false);

    new AnalyticsAPI()
      .updateChart(data)
      .then((res) => {
        setSuccessMessage("Chart Updated!");

        // const id = res.workspaceId;

        setState({ ...state, modalVisible: false });

        // if (currentWorkspaceData._id !== id) {
        //   setState({ ...state, modalVisible: false });

        //   cancelLoading();

        //   return console.log(id, currentWorkspaceData._id);
        // }

        getWorkspaceById({ id: currentWorkspace });

        getWorkspaceData(
          currentBusiness,
          currentWorkspace,
          false,
          role,
          role !== "admin" ? memberUserId : ""
        );

        setShouldCached(true);
      })
      .catch((err) => {
        setState({ ...state, modalVisible: false });

        cancelLoading();

        return setErrorMessage(err.message);
      });
  };

  const deleteChart = async (chart, newLayout) => {
    try {
      startLoading();

      const res = await new AnalyticsAPI().deleteChart({
        dashboardId: currentWorkspace,
        params: chart,
        layout: newLayout,
      });

      if (res.error) {
        cancelLoading();

        return setErrorMessage(res.error);
      }

      // setCurrentWorkspaceData((prevState) => ({
      //   ...prevState,
      //   layout: newLayout,
      // }));

      await getWorkspaceById({ id: currentWorkspace });

      cancelLoading();

      setSuccessMessage("Chart Deleted!");
    } catch (err) {
      cancelLoading();
      return setErrorMessage(err.message);
    }
  };

  const updateLayout = (layout) => {
    setCurrentWorkspaceData((prevState) => ({
      ...prevState,
      layout: layout,
    }));

    new AnalyticsAPI()
      .setLayout({
        dashboardId: currentWorkspace,
        layout: layout,
      })
      .then((res) => {})
      .catch(() => {
        setErrorMessage("Some error");
      });
  };

  const createNewWorkSpace = (data) => {
    startLoading();

    setState({ ...state, showWorkSpaceModal: false });

    return new AnalyticsAPI()
      .createWorkspace(data)
      .then(async (res) => {
        if (res.error) {
          cancelLoading();
          return setErrorMessage(res.error);
        }

        setSuccessMessage("Workspace Created");

        setState({ ...state, loader: false });

        return await getWorkspaces(currentBusiness);
      })
      .catch((err) => {
        cancelLoading();
        return setErrorMessage(err.message);
      });
  };

  const editWorkSpaceMeta = (data) => {
    startLoading();

    setState({ ...state, showWorkSpaceModal: false });

    new AnalyticsAPI()
      .editWorkSpaceMeta(data)
      .then(async (res) => {
        if (res.error) {
          cancelLoading();

          return setErrorMessage(res.error);
        }
        setSuccessMessage("Workspace Edited");

        getWorkspaceById({ id: currentWorkspace, role: userRole || "" });

        await getWorkspaces(currentBusiness);
      })
      .catch((err) => {
        console.log(err);
        cancelLoading();

        return setErrorMessage(err.message);
      });
  };

  const deleteWorkspace = (did) => {
    startLoading();

    setState({ ...state, showWorkSpaceModal: false });

    new AnalyticsAPI()
      .deleteDashboard({ dashboardId: did })
      .then(async (res) => {
        if (res.error) {
          cancelLoading();

          return setErrorMessage(res.error);
        }

        setSuccessMessage("Workspace deleted");

        setState({ ...state, loader: false });

        await getWorkspaces(currentBusiness);
      })
      .catch((err) => {
        cancelLoading();
        setErrorMessage(err.message);
      });
  };

  const getUersRoleBid = (bid, role) => {
    setState({ ...state, showWorkSpaceModal: false });
    startLoading();

    new AnalyticsAPI()
      .fetchUsersBidandRole(bid, role)
      .then((res) => {
        formatUsernUserId(res.users);

        return cancelLoading();
      })
      .catch((err) => {
        cancelLoading();
        setErrorMessage(err.message);
      });
  };

  const toggleVisible = (arg) => {
    return setState((prevState) => ({ ...prevState, isVisible: arg }));
  };

  const formatUsernUserId = (users) => {
    let temp = [];
    users.forEach((el) => {
      temp.push({ key: el.userId, value: el.Name });
    });
    setUserRoleFilterData(temp);
  };

  const checkDepartment = async (bid, dashboardId) => {
    try {
      isDepartmentExist(false);
      setDepartment(false);

      const departmentId = localStorage.getItem("departmentId") || null;
      const teamId = localStorage.getItem("teamId") || null;

      const res = await fetchIsDepartmentHead(departmentId, teamId);

      if (res.error) {
        setDepartment(false);
        setUserRole(null);

        // Get all available workspaces
        await getWorkspaces(bid, "");
        setCurrentBusiness(bid);

        // Set Current DashboardId from the url params  EG: /analytics/1344
        setCurrentWorkspace(dashboardId);

        if (dashboardId && dashboardId.length >= 24) {
          getWorkspaceById({ id: dashboardId, role: "" });
        } else {
          setNoteMessage("Please Select your workspace.");
          setCurrentWorkspace(undefined);
        }

        return isDepartmentExist(false);
      }

      // SET Current user Role
      setUserRole(res.role);

      // Get all available workspaces
      await getWorkspaces(bid, `?role=${res.role}`);
      setCurrentBusiness(bid);

      // Set Current DashboardId from the url params  EG: /analytics/1344
      setCurrentWorkspace(dashboardId);

      if (dashboardId && dashboardId.length >= 24) {
        getWorkspaceById({ id: dashboardId, role: res.role });
      } else {
        setNoteMessage("Please Select your workspace.");
        setCurrentWorkspace(undefined);
      }

      isDepartmentExist(true);
      setDepartment(true);
    } catch (err) {
      isDepartmentExist(false);
      setDepartment(false);
      return setErrorMessage(err.message);
    }
  };

  const openAddChartDrawer = () => {
    if (isLoading || state.editInstance) return;

    return setDrawers((prevState) => ({
      ...prevState,
      openAddChartDrawer: true,
    }));
  };

  const value = {
    isOpenWorkspaceModal,
    openWorkspaceModal,
    workspaces,
    setWorkspaces,
    currentWorkspace,
    setCurrentWorkspace,
    leads,
    setLeads,
    openAddChartDrawer,
    state,
    setState,
    currentBusiness,
    setCurrentBusiness,
    dataFields,
    toggleWorkspaceModal,
    closeModal,
    toggleVisible,
    getBusiness,
    _currentBusiness,
    editChart,
    deleteChart,
    createChartService,
    updateLayout,
    workspaceData,
    setWorkspaceData,
    getWorkspaceData,
    drawers,
    setDrawers,
    createNewWorkSpace,
    editWorkSpaceMeta,
    getUersRoleBid,
    userRoleFilterData,
    setUserRoleFilterData,
    deleteWorkspace,
    shouldCached,
    isLoading,
    setShouldCached,
    startLoading,
    cancelLoading,
    noteConfig,
    setCurrentWorkspaceData,
    currentWorkspaceData,
    setNoteConfig,
    getWorkspaceById,
    setNoteMessage,
    memberUserId,
    setMemberUserId,
    editChartService,
    checkDepartment,
    userRole,
    setDataFields,
    getLeadDataFields,
  };

  return (
    <AnalyticsContext.Provider value={value}>
      {children}
    </AnalyticsContext.Provider>
  );
};

export default AnalyticsProvider;
