import React, { useContext } from "react";

export const TeamMember = React.createContext();

export const useTeamMembers = () => useContext(TeamMember);

const TeamMemberContext = ({ children }) => {
  const [invitedMembers, setInvitedMembers] = React.useState([]);
  const [inviteSuccess, setInviteSuccess] = React.useState(false);
  const [deleteSuccess, setDeleteSuccess] = React.useState(false);
  const [showInviteForm, setShowInviteForm] = React.useState(false);
  const [totalMembersInDb, setTotalMembersInDb] = React.useState();
  const [acceptedFilter, setAcceptedFilter] = React.useState("");
  const [pageNo, setPageNo] = React.useState(1);
  const [currentPageRecord, setCurrentPageRecord] = React.useState();

  const value = {
    invitedMembers,
    setInvitedMembers,
    inviteSuccess,
    setInviteSuccess,
    deleteSuccess,
    setDeleteSuccess,
    showInviteForm,
    setShowInviteForm,
    acceptedFilter,
    setAcceptedFilter,
    pageNo,
    setPageNo,
    totalMembersInDb,
    setTotalMembersInDb,
    currentPageRecord,
    setCurrentPageRecord,
  };

  return <TeamMember.Provider value={value}>{children}</TeamMember.Provider>;
};

export default TeamMemberContext;
