/** @format */

import { cancelLoading, error, startLoading } from "../main";
import firebase, { db } from "../../../firebase";
import { authSteps } from "../auth/auth";
import { storeTwitterCredentials } from "../../../api/Auth";

export const signInWithTwitter = (businessId) => {
  return (dispatch) => {
    dispatch(startLoading());

    const provider = new firebase.auth.TwitterAuthProvider();

    provider.setCustomParameters({
      lang: "en",
    });

    firebase
      .auth()
      .signInWithPopup(provider)
      .then((result) => {
        // This gives you a the Twitter OAuth 1.0 Access Token and Secret.
        // You can use these server side with your app's credentials to access the Twitter API.
        const token = result.credential.accessToken;
        const secret = result.credential.secret;

        storeTwitterCredentials(businessId, {
          token,
          secret,
        })
          .then((res) => {
            if (res.error) {
              return dispatch(error(res.error));
            }

            return firebase
              .auth()
              .currentUser.delete()
              .then((data) => {
                dispatch(authSteps(14));
                dispatch(cancelLoading());
              })
              .catch((err) => dispatch(error(err)));
          })
          .catch((err) => {
            dispatch(error(err.message));
          });
      })
      .catch(function (error) {
        dispatch(cancelLoading());
        // Handle Errors here.
        // const errorCode = error.code;
        // const errorMessage = error.message;
        // The email of the user's account used.
        // const email = error.email;
        // The firebase.auth.AuthCredential type that was used.
        // const credential = error.credential;
        // ...
      });
  };
};

export const signInWithTwitterConnect = (
  businessId,
  setErrorMessage,
  setSuccessMessage,
  history
) => {
  return (dispatch) => {
    dispatch(startLoading());

    const provider = new firebase.auth.TwitterAuthProvider();

    provider.setCustomParameters({
      lang: "en",
    });

    firebase
      .auth()
      .signInWithPopup(provider)
      .then((result) => {
        // This gives you a the Twitter OAuth 1.0 Access Token and Secret.
        // You can use these server side with your app's credentials to access the Twitter API.
        const token = result.credential.accessToken;
        const secret = result.credential.secret;

        db.collection("business")
          .doc(businessId)
          .update({
            "social_media_connections.twitter": {
              access_token: token,
              secret: secret,
            },
          })
          .then((res) => {
            firebase
              .auth()
              .currentUser.delete()
              .then((data) => {
                setSuccessMessage(
                  "You have successfully linked your Twitter account!"
                );
                dispatch(cancelLoading());
                history.push(`/business/${businessId}/social-profiles`);
              })
              .catch((err) => {
                setErrorMessage(err);
                dispatch(error(err));
              });
          })
          .catch((err) => {
            setErrorMessage(err);
            dispatch(error(err));
          });
      })
      .catch(function (error) {
        dispatch(error(error.message));
        // Handle Errors here.
        // const errorCode = error.code;
        const errorMessage = error.message;
        setErrorMessage(errorMessage);

        // The email of the user's account used.
        // const email = error.email;
        // The firebase.auth.AuthCredential type that was used.
        // const credential = error.credential;
        // ...
      });
  };
};
