/** @format */

import decryptResponse from "../../Shared/decryptResponse";
import encryptResponse from "../../Shared/encryptResponse";

const { API } = require("../../Backend");

export const checkForAddress = async () => {
  try {
    const response = await fetch(`${API}/address/validate`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
      },
    });

    return decryptResponse({ data: await response.json() });
  } catch (err) {
    return { error: err.message };
  }
};

export const getUserAddress = async () => {
  try {
    const res = await fetch(`${API}/address/get`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "Content-Type": "application/json",
      },
    });
    return decryptResponse({ data: await res.json() });
  } catch (err) {
    return { error: err.message };
  }
};

export const updateAddress = async (data) => {
  try {
    const res = await fetch(`${API}/address/add`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "Content-Type": "application/json",
      },
      body: JSON.stringify(encryptResponse(data)),
    });
    return await res.json();
  } catch (err) {
    return { error: err.message };
  }
};
