import React, { useContext, useState } from "react";

export const WhatsAppTemplate = React.createContext();

export const useWhatsAppTemplate = () => useContext(WhatsAppTemplate);

const WhatsAppTemplateContext = ({ children }) => {
  const [templatesData, setTemplatesData] = useState([]);
  const [newTemplateData, setNewTemplateData] = React.useState(null);
  const [showTemplateForm, setShowTemplateForm] = React.useState(false);
  const [showPreviewForm, setShowPreviewForm] = React.useState(false);
  const [showDefault, setShowDefault] = React.useState(true);

  const [editTemplateId, setEditTemplateId] = React.useState("");
  const [timeStamp, setTimeStamp] = React.useState(new Date().getTime());
  const [quickReplyButton, setQuickReplyButton] = React.useState([]);
  const [callToActionButton, setCallToActionButton] = React.useState([]);

  const [headerTypeData, setHeaderTypeData] = React.useState([
    {
      name: "Text",
      value: "TEXT",
    },
    {
      name: "Image",
      value: "IMAGE",
    },
    {
      name: "Video",
      value: "VIDEO",
    },
    {
      name: "Documents",
      value: "DOCUMENT",
    },
  ]);
  const [buttonTypeData, setButtonTypeData] = useState([
    {
      name: "CALL_TO_ACTION",
      label: "Call to Action",
    },
    {
      name: "QUICK_REPLY",
      label: "Quick Reply",
    },
  ]);
  const [templateCategoryData] = useState([
    { value: "TRANSACTIONAL", name: "TRANSACTIONAL" },
    { value: "MARKETING", name: "MARKETING" },
    { value: "OTP", name: "OTP" },
  ]);
  const AddQuickReplyButton = () => {
    setQuickReplyButton((buttons) => {
      return [
        ...buttons,
        {
          type: "QUICK_REPLY",
          text: "",
        },
      ];
    });
  };
  const AddCallToActionButton = () => {
    setCallToActionButton((buttons) => {
      return [
        ...buttons,
        {
          type: "",
          text: "",
        },
      ];
    });
  };
  const removeQuickReply = (index) => {
    let quickButtons = quickReplyButton;
    quickButtons.splice(index, 1);
    setQuickReplyButton(quickButtons);
    setTimeStamp(new Date().getTime());
  };

  const removeCallAction = (index) => {
    let callActionButton = callToActionButton;
    callActionButton.splice(index, 1);
    setCallToActionButton(callActionButton);
    setTimeStamp(new Date().getTime());
  };
  const handleChangeQuickReply = (index) => (e) => {
    e.preventDefault();

    console.log("value", e.target.value);
    /* setQuickReplyButton((s) => {
      quickReplyButton[index].text = e.target.value;
      return quickReplyButton;
    });
    setTimeStamp(new Date().getTime()); */
    quickReplyButton[index]["text"] = e.target.value;
    setQuickReplyButton(quickReplyButton);
    setTimeStamp(new Date().getTime());
  };

  const changeCallActionByNameAndValue = (index, name, value) => {
    callToActionButton[index][name] = value;
    setCallToActionButton(callToActionButton);
    setTimeStamp(new Date().getTime());
  };

  const handleChangeCallAction = (index) => (e) => {
    e.preventDefault();
    let name = e.target.name;
    callToActionButton[index][name] = e.target.value;
    setCallToActionButton(callToActionButton);
    setTimeStamp(new Date().getTime());
    /* 
     setCallToActionButton((s) => {
      callToActionButton[index][name] = e.target.value;
      return callToActionButton;
    });  */
  };

  const value = {
    showDefault,
    setShowDefault,
    templatesData,
    setTemplatesData,
    showTemplateForm,
    headerTypeData,
    setHeaderTypeData,
    setShowTemplateForm,
    setShowPreviewForm,
    showPreviewForm,
    quickReplyButton,
    setQuickReplyButton,
    callToActionButton,
    setCallToActionButton,
    AddQuickReplyButton,
    handleChangeQuickReply,
    handleChangeCallAction,
    changeCallActionByNameAndValue,
    buttonTypeData,
    setButtonTypeData,
    templateCategoryData,
    AddCallToActionButton,
    removeQuickReply,
    removeCallAction,
    newTemplateData,
    setNewTemplateData,
    editTemplateId,
    setEditTemplateId,
    timeStamp,
    setTimeStamp,
  };

  return (
    <WhatsAppTemplate.Provider value={value}>
      {children}
    </WhatsAppTemplate.Provider>
  );
};

export default WhatsAppTemplateContext;
