import * as actions from "../actions/actionTypes";

const initialState = {
  leads: null,
  allLeads: null,
  currentLeadData: null,
  currentLeadId: null,
  page: {},
  error: false,
  hasMore: false,
  leadCount: {},
  limit: 50,
  departmentName: null,
};

const getDepartmentLeads = (state, action) => {
  return {
    ...state,
    leads:
      (state.leads && [...new Set([...state.leads, ...action.leads])]) ||
      action.leads,
    page: { ...state.page, [action.tabName]: action.page || 1 },
    leadCount: { ...state.leadCount, [action.tabName]: action.leadCount },
  };
};

export const getAllDepartmentLeads = (state, action) => {
  return {
    ...state,
    allLeads:
      (state.allLeads && [...new Set([...state.allLeads, ...action.leads])]) ||
      action.leads,
    departmentName: `- ${action.departmentName}`,
    page: { ...state.page, [action.tabName]: action.page },
    leadCount: { ...state.leadCount, [action.tabName]: action.leadCount },
  };
};

const addDepartmentLeads = (state, action) => {
  return {
    ...state,
    leads: action.leads,
  };
};

export const startLoadingDepartment = (state, action) => {
  return {
    ...state,
    loading: true,
  };
};

export const cancelLoadingDepartment = (state, action) => {
  return {
    ...state,
    loading: false,
  };
};

export const setHasMoreDepartment = (state, action) => {
  return {
    ...state,
    hasMore: action.hasMore,
  };
};

export const errorDepartment = (state, action) => {
  return {
    ...state,
    loading: false,
    error: action.error,
  };
};

export const getDepartmentFullLeadData = (state, action) => {
  return {
    ...state,
    currentLeadData: action.currentLeadData,
    currentLeadId: action.currentLeadId,
  };
};

export const resetDepartmentPageCount = (state, action) => {
  return {
    ...state,
    page: {},
  };
};

export const resetDepartmentLeadCount = (state, action) => {
  return {
    ...state,
    leadCount: {},
  };
};

export const clearDepartmentLeads = (state, action) => {
  return {
    ...state,
    leads: null,
  };
};

export const clearAllDepartmentLeads = (state, action) => {
  return {
    ...state,
    allLeads: null,
  };
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.GET_DEPARTMENT_LEADS:
      return getDepartmentLeads(state, action);
    case actions.GET_DEPARTMENT_FULL_LEAD_DATA:
      return getDepartmentFullLeadData(state, action);
    case actions.START_LOADING_DEPARTMENT:
      return startLoadingDepartment(state, action);
    case actions.CANCEL_LOADING_DEPARTMENT:
      return cancelLoadingDepartment(state, action);
    case actions.ERROR_DEPARTMENT:
      return errorDepartment(state, action);
    case actions.HAS_MORE_DEPARTMENT:
      return setHasMoreDepartment(state, action);
    case actions.ADD_DEPARTMENT_LEADS:
      return addDepartmentLeads(state, action);
    case actions.GET_ALL_DEPARTMENT_LEADS:
      return getAllDepartmentLeads(state, action);
    case actions.RESET_DEPARTMENT_PAGE_COUNT:
      return resetDepartmentPageCount(state, action);
    case actions.CLEAR_ALL_DEPARTMENT_LEADS:
      return clearAllDepartmentLeads(state, action);
    case actions.CLEAR_DEPARTMENT_LEADS:
      return clearDepartmentLeads(state, action);
    case actions.RESET_DEPARTMENT_LEAD_COUNT:
      return resetDepartmentLeadCount(state, action);
    default:
      return state;
  }
};

export default reducer;
