import { useCallback, useEffect, useRef, useState } from "react";
import { connect } from "react-redux";
import { motion } from "framer-motion";
import { Button } from "@material-ui/core";
import PhoneInput from "react-phone-input-2";

import { updateObject } from "Shared/Utility";
import { getUserProfile, updateUserProfile } from "api/user";
import * as actions from "Store/actions/index";
import { ToastProvider } from "react-toast-notifications";
import { CustomButtonToast } from "Components/UI/Toasts/Toast";
import { UIButton, Input, Modal2 } from "Components/exports";
import { useSidebar } from "contexts/SidebarProvider/SidebarProvider";

import ResetPasswordPage from "./ResetPasswordPage/ResetPasswordPage";

import classes from "./MyProfile.module.css";
import "react-phone-input-2/lib/material.css";
import useLocalStorage from "hooks/useLocalStorage";

const MyProfile = ({ index, ...props }) => {
  const [isNextPage, openNextPage] = useState(false);

  const { activeMenu, closeActiveMenu } = useSidebar();

  const variants = {
    open: { opacity: 1, x: "0%", y: "0%", pointerEvents: "all" },
    closed: { opacity: 0, x: "-100%", y: "0%", pointerEvents: "none" },
  };

  const [userData, setUserData] = useState({
    name: {
      value: "",
      elementType: "text",
      required: true,
      type: "text",
      placeholder: "Name",
      id: 1,
    },
    mobile_number: {
      value: "",
      elementType: "text",
      required: true,
      type: "number",
      placeholder: "Mobile Number",
      id: 2,
      maxLength: 10,
      minLength: 10,
      readOnly: true,
    },
    email: {
      value: "",
      elementType: "email",
      required: true,
      type: "Email",
      placeholder: "Email id",
      id: 3,
    },
  });

  const getUserProfileRef = useRef(() => {});

  getUserProfileRef.current = async () => {
    if (!props.user && userData.name.value.length < 1) {
      const data = await getUserProfile();
      if (data.success) {
        for (let key in userData) {
          if (data.payload[key] !== undefined) {
            setUserData((state) => ({
              ...state,
              [key]: {
                ...state[key],
                value: data.payload[key],
              },
            }));
          }
        }
        props.cancelLoading();
      } else {
        props.error(data.error);
        setToast({
          ...toast,
          success: null,
          error: data.error,
          disabled: false,
        });
      }
    } else {
      for (let key in props.user) {
        if (props.user[key] !== undefined) {
          setUserData((state) => ({
            ...state,
            [key]: {
              ...state[key],
              value: props.user[key],
            },
          }));
        }
      }
    }
  };

  const [toast, setToast] = useState({
    error: null,
    success: null,
    disabled: false,
  });
  const [callingCode, setCallingCode] = useLocalStorage("callingCode", null);
  const [country, setCountry] = useLocalStorage("country", null);
  const getCountry = useCallback(async () => {
    try {
      const visitorReq = await fetch("//api.ipregistry.co/?key=tryout", {
        method: "GET",
      });
      if (visitorReq.ok) {
        const visitor = await visitorReq.json();
        setCallingCode(visitor?.location?.country?.calling_code);
        setCountry(visitor?.location?.country?.code.toLowerCase());
      } else {
        setCountry("in");
      }
    } catch (error) {
      console.error(error);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [setCountry]);

  useEffect(() => {
    (country === null || callingCode === null) && getCountry();
  }, [country, callingCode, getCountry]);

  useEffect(() => {
    getUserProfileRef.current();
  }, []);

  const inputChangedHandler = (event, inputIdentifier) => {
    let updatedFormElement;

    updatedFormElement = updateObject(userData[inputIdentifier], {
      value: event.target.value,
    });

    const updatedOrderForm = updateObject(userData, {
      [inputIdentifier]: updatedFormElement,
    });

    setUserData(updatedOrderForm);
  };

  const inputChangedFlag = (value) => {
    if (value.length !== 7) {
      const updatedOrderForm = updateObject(userData, {
        mobile_number: {
          value,
        },
      });
      setUserData(updatedOrderForm);
    }
  };

  const formElementsArray = [];
  for (let key in userData) {
    formElementsArray.push({
      id: key,
      config: userData[key],
    });
  }
  return (
    <>
      <motion.section
        initial={{ opacity: 0, x: "-100%", y: "0%", pointerEvents: "none" }}
        transition={{ duration: 0.5, ease: [0.43, 0.13, 0.23, 0.96] }}
        animate={activeMenu === index ? "open" : "closed"}
        variants={variants}
        className={classes.MyProfile}
      >
        <Modal2 close={closeActiveMenu} title="My Profile" />
        <ToastProvider>
          <CustomButtonToast
            show={toast.error !== null || toast.success !== null}
            appearance={
              toast.error ? "error" : toast.success ? "success" : "info"
            }
            message={toast.error || toast.success}
          />
        </ToastProvider>
        <div className={classes.MyProfile_Form}>
          <form
            onSubmit={(e) => {
              setToast({
                ...toast,
                error: null,
                success: null,
                disabled: true,
              });

              e.preventDefault();

              updateUserProfile({
                email: userData.email.value,
                name: userData.name.value,
              })
                .then((data) => {
                  if (data.error) {
                    setToast({
                      ...toast,
                      success: null,
                      error: data.error,
                      disabled: false,
                    });
                  } else {
                    setToast({
                      ...toast,
                      success: data.message,
                      error: null,
                      disabled: false,
                    });
                    props.fetchUserDetail();
                  }
                })
                .catch((err) => {
                  setToast({
                    ...toast,
                    success: null,
                    error: err,
                    disabled: false,
                  });
                });
            }}
          >
            {formElementsArray.map((formElement) => {
              console.log(formElement.config.type);
              return formElement.config.type === "number" ? (
                <PhoneInput
                  key={formElement.id}
                  value={formElement.config.value}
                  country={country}
                  enableAreaCodes={true}
                  label="Mobile No."
                  onChange={(phone) => inputChangedFlag(phone)}
                  inputProps={{
                    id: formElement.id,
                    className: `${classes.PhoneInput} form-control`,
                    name: formElement.config.type,
                    placeholder: formElement.config.placeholder,
                    maxLength: formElement.config.maxLength,
                    minLength: formElement.config.minLength,
                    readOnly: formElement.config.readOnly,
                  }}
                />
              ) : (
                <Input
                  key={formElement.id}
                  type={formElement.config.elementType}
                  name={formElement.config.type}
                  value={formElement.config.value}
                  inputChangedHandler={inputChangedHandler}
                  required={formElement.config.required}
                  id={formElement.id}
                  placeholder={formElement.config.placeholder}
                  maxLength={formElement.config.maxLength}
                  minLength={formElement.config.minLength}
                  readOnly={formElement.config.readOnly}
                />
              );
            })}
            <UIButton
              disabled={toast.disabled}
              svgStyle={{ marginLeft: "10px" }}
            >
              Save Changes
            </UIButton>
          </form>
        </div>

        <Button
          classes={{ root: classes.Change_Password_Btn }}
          onClick={() => openNextPage(true)}
        >
          Change your password &gt;
        </Button>
      </motion.section>

      <ResetPasswordPage
        mobileNumber={userData.mobile_number.value}
        open={isNextPage}
        closePage={() => openNextPage(false)}
      />
    </>
  );
};
const mapStateToProps = (state) => {
  return {
    user: state.user.user,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    startLoading: () => dispatch(actions.startLoading()),
    cancelLoading: () => dispatch(actions.cancelLoading()),
    error: (err) => dispatch(actions.error(err)),
    fetchUserDetail: () => dispatch(actions.fetchUserDetail()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(MyProfile);
