/** @format */

import * as actions from "../actionTypes";
import { cancelLoading, startLoading } from "../main";
import { getAllFonts } from "../../../api/Font";

export const getFonts = (fonts) => {
  return {
    type: actions.GET_FONTS,
    fonts: fonts,
  };
};

export const fetchFonts = () => {
  return (dispatch) => {
    dispatch(startLoading());
    getAllFonts()
      .then((data) => {
        if (data.message) {
          console.log(data);
          dispatch(getFonts(data.data.fonts));
          cancelLoading();
        } else {
          cancelLoading();
        }
      })
      .catch((err) => {
        dispatch(err(err));
      });
  };
};
