import classes from "./Modal2.module.css";
import React from "react";
import BackBtn from "../BackBtn/BackBtn";

const Modal2 = ({ title, profile, link, close }) => {
  return (
    <>
      <div className={classes.Header_Profile}>
        {profile && (
          <>
            <div className={classes.Business_Img}>
              {profile && <img src={profile.url} alt="profile" />}
            </div>
            <p>{profile && profile.business_name}</p>
          </>
        )}
      </div>

      <header style={{ marginTop: "1%" }} className={classes.Modal2}>
        <BackBtn close={close && close} link={link && link} />
        <div className={classes.Modal_Title}>
          <h1>{title}</h1>
        </div>
      </header>
    </>
  );
};

export default Modal2;
