import CryptoJS from "crypto-js";
import { DECRYPTION_KEY } from "../Backend";

export default function encryptResponse(response) {
  return {
    enc: CryptoJS.AES.encrypt(
      JSON.stringify(response),
      DECRYPTION_KEY
    ).toString(),
  };
}

export function simpleEncrypt(response) {
  return {
    enc: CryptoJS.HmacSHA1(JSON.stringify(response), DECRYPTION_KEY).toString(),
  };
}
