import React, { useContext, useEffect, useState } from "react";
import io, { Socket } from "socket.io-client";
import { socketEndpoint } from "../Backend";
import { useApp } from "./AppProvider";

export type SocketContextType = {
  socket: Socket;
};

export const SocketContext = React.createContext<SocketContextType | undefined>(
  undefined
);

export const useSocket = () => {
  let context = useContext(SocketContext);

  // If context is undefined, we know we used RadioGroupItem
  // outside of our provider so we can throw a more helpful
  // error!
  if (context === undefined) {
    throw Error(
      "Item must be used inside of a Object, otherwise it will not function correctly."
    );
  }

  // Because of TypeScript's type narrowing, if we make it past
  // the error the compiler knows that context is always defined
  // at this point, so we don't need to do any conditional
  // checking on its values when we use this hook!
  return context;
};

const ENDPOINT = socketEndpoint;

export const SocketProvider: React.FC = ({ children }) => {
  const [socket, setSocket] = useState<Socket>();

  const { deviceSession } = useApp();
  const businessId = localStorage.getItem("businessId");
  const userId = localStorage.getItem("userId");

  useEffect(() => {
    if (!deviceSession) return;

    const newSocket = io(ENDPOINT, {
      forceNew: true,
      transports: ["polling"],
      query: {
        userId: localStorage.getItem("userId"),
        deviceId: deviceSession,
        businessId: businessId ? businessId : null,
      },
    });

    setSocket(newSocket);

    return () => {
      socket && socket.disconnect();

      socket && socket.off();
    };
  }, [deviceSession, businessId, userId]);

  return (
    <SocketContext.Provider value={{ socket }}>
      {children}
    </SocketContext.Provider>
  );
};

export default SocketProvider;
