import { Button } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import clsx from "clsx";
const styles = {
  root: {
    marginLeft: "10%",
    display: "flex",
    justifyContent: "space-between",
    padding: "15px 20px 15px 20px",
    borderRadius: "8px",
    fontSize: "16px",
    fontWeight: "bold",
    background: "linear-gradient(45deg, #12B0EE 30%, #12B0EE 90%)",
    border: 0,
    color: "white",
    height: 48,
    textTransform: "none",

    "&:hover": {
      boxShadow: "0 8px 8px 0 rgba(50, 197, 255, 0.2)",
    },
    "&:disabled": {
      color: "#FFF !important",
    },
  },
};

const UIButton = ({
  classes,
  children,
  className,
  disabled,
  svgStyle,
  arrowColor = "#fff",
  hideArrow = false,
  showLoader = false,
  style,
  type = "submit",
  ...props
}) => {
  // We can inject some CSS into the DOM.

  return (
    <Button
      disabled={disabled}
      type={type}
      className={clsx(className)}
      classes={classes}
      style={style}
      onClick={props.onClick}
      {...props}
    >
      {showLoader && (
        <i
          style={{ marginRight: "8px" }}
          className={`fa fa-spinner fa-spin`}
        ></i>
      )}
      {children}
      {!hideArrow && (
        <svg
          width="14"
          height="10"
          viewBox="0 0 14 10"
          fill="none"
          xmlns="//www.w3.org/2000/svg"
          style={(svgStyle, { marginLeft: "10px" })}
        >
          <path
            fillRule="evenodd"
            clipRule="evenodd"
            d="M11.3692 4.19223L8.28352 1.00205L9.25275 0L14 4.90798L9.43077 10L8.46154 8.99796L11.3692 5.62372H0V4.19223H11.3692Z"
            fill="white"
          />
        </svg>
      )}
    </Button>
  );
};

export default withStyles(styles)(UIButton);
