/** @format */

import { AuthenticatedHeaders } from "utils/ApiHeaders";
import { API, API_V2 } from "../../Backend";
import encryptResponse from "../../Shared/encryptResponse";

export const getUserProfile = async () => {
  try {
    const res = await fetch(`${API_V2}/user`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const updateUserProfile = async (data) => {
  try {
    const res = await fetch(`${API_V2}/user`, {
      method: "PUT",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
      body: JSON.stringify(data),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const validateEmail = async (data) => {
  try {
    const response = await fetch(`${API}/user/validate-email`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify(encryptResponse(data)),
    });
    return await response.json();
  } catch (err) {
    return { error: err.message };
  }
};

export const NewUserEmailPass = async (data, encPass) => {
  try {
    const res = await fetch(`${API}/new-user-email-password`, {
      method: "PUT",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-userId": localStorage.getItem("userId"),
        "X-password": encPass,
      },
      body: JSON.stringify(data),
    });
    return res.json();
  } catch (err) {
    return { error: err.message };
  }
};

export const getUserDetail = async () => {
  try {
    const res = await fetch(`${API_V2}/user`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const invitedSignup = async (data) => {
  try {
    const res = await fetch(`${API_V2}/auth/invited-sign-up`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });
    return res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const Signup = async (data) => {
  try {
    const res = await fetch(`${API_V2}/auth/sign-up`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });

    const result = await res.json();

    const user = await fetch(`${API_V2}/user`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${result.payload.token}`,
      },
    });
    const userData = await user.json();
    return { ...result.payload, user: userData };
  } catch (err) {
    return { message: err.message };
  }
};
