import React, { useContext } from "react";

export const Dailog = React.createContext();

export const useDailog = () => useContext(Dailog);

const DailogContext = ({ children }) => {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const value = {
    open,
    setOpen,
    handleClickOpen,
    handleClose,
  };

  return <Dailog.Provider value={value}>{children}</Dailog.Provider>;
};

export default DailogContext;
