/** @format */

import * as actions from "../actions/actionTypes";

const initialState = {
  templates: [],
  loading: false,
  error: null,
};

const storeTemplates = (state, action) => {
  return {
    ...state,
    loading: false,
    templates: [action.templates],
  };
};

const fetchTemplateFailed = (state, action) => {
  return {
    ...state,
    loading: false,
    error: action.error,
  };
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.STORE_TEMPLATES:
      return storeTemplates(state, action);
    case actions.FETCH_TEMPLATE_FAILED:
      return fetchTemplateFailed(state, action);
    default:
      return state;
  }
};

export default reducer;
