import React, { useContext } from "react";

export const Model = React.createContext();

export const useModel = () => useContext(Model);

const ModelContext = ({ children }) => {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const value = {
    open,
    setOpen,
    handleClickOpen,
    handleClose,
  };

  return <Model.Provider value={value}>{children}</Model.Provider>;
};

export default ModelContext;
