import {
  createOrder,
  captureOrder,
  fetchOrder,
  saveRenewedOrderOnDatabase,
  renewOrder,
} from "../../../api/Order";
import { KEY } from "../../../Backend";
import { cancelLoading, error, startLoading } from "../main";
import { getPlanDetail } from "../../../api/Plan";
import { fetchBusinessOrders } from "../business/business";

export const renewalOrderHandler = (
  amount,
  planId,
  currentPlanExpiry,
  businessId,
  couponDetail,
  gstNumber,
  history,
  gstPercentage,
  gstAmount,
  message
) => {
  return (dispatch) => {
    dispatch(startLoading());
    if (amount > 0) {
      getPlanDetail(planId).then((planDetail) => {
        return createOrder({ amount: amount })
          .then((amount) => {
            const options = {
              key: KEY,
              name: "Easy Social",
              description: "Subscription Renewal",
              order_id: amount.id,
              handler: async (response) => {
                try {
                  const paymentId = response.razorpay_payment_id;

                  return captureOrder(paymentId, { amount: amount }).then(
                    async (response) => {
                      try {
                        const data = await fetchOrder(paymentId);

                        const paymentData = {
                          amount: amount.amount / 100,
                          paymentId: paymentId,
                          status: 1,
                        };

                        sessionStorage.setItem(
                          "paymentData",
                          JSON.stringify(paymentData)
                        );

                        await saveRenewedOrderOnDatabase(
                          {
                            order: data,
                            plan: {
                              expiry: new Date(
                                currentPlanExpiry.getFullYear(),
                                currentPlanExpiry.getMonth(),
                                currentPlanExpiry.getDate() +
                                  planDetail.data.details.validity
                              ),
                              start: currentPlanExpiry,
                              planId: planId,
                            },
                            gstInfo: {
                              gstPercentage,
                              gstAmount,
                            },
                            couponDetail,
                            gstNumber,
                          },
                          businessId
                        )
                          .then((order) => {
                            // Order is now saved
                            renewOrder(
                              {
                                validity: planDetail.data.details.validity,
                                planId: planDetail.data.planId,
                                paymentId: paymentId,
                              },
                              businessId
                            )
                              .then((data) => {
                                if (data.error) {
                                  return message.setErrorMessage(data.error);
                                }
                                dispatch(fetchBusinessOrders(businessId));
                                sessionStorage.removeItem("renewDetail");
                                history.push("/user/payments/renewals");
                                dispatch(cancelLoading());
                                return message.setSuccessMessage(
                                  "You have successfully renewed your plan! Thank You!"
                                );
                              })
                              .catch((error) => {
                                message.setErrorMessage(error.message);

                                return dispatch(error(error.message));
                              });
                          })
                          .catch((err) => error(err.message));
                      } catch (err) {
                        const paymentData_1 = {
                          amount: amount.amount / 100,
                          status: 0,
                        };
                        sessionStorage.setItem(
                          "paymentData",
                          JSON.stringify(paymentData_1)
                        );
                        message.setErrorMessage(error.message);

                        dispatch(error(err.message));
                      }
                    }
                  );
                } catch (err) {
                  const paymentData = {
                    amount: amount.amount / 100,
                    status: 0,
                  };
                  sessionStorage.setItem(
                    "paymentData",
                    JSON.parse(paymentData)
                  );
                  message.setErrorMessage(error.message);

                  dispatch(error(err.message));
                }
              },
              theme: {
                color: "#686CFD",
              },
            };
            const rzp1 = new window.Razorpay(options);
            rzp1.open();
          })
          .catch((err) => {
            message.setErrorMessage(error.message);

            return dispatch(error(err.message));
          });
      });
    }
  };
};
