// backend route import
import { API, API_V2 } from "Backend";
import { AuthenticatedHeaders } from "utils/ApiHeaders";
import decryptResponse from "../../Shared/decryptResponse";

// upload file
export default class WalletAPI {
  public static getWalletBalance = async () => {
    try {
      const res = await fetch(`${API_V2}/wallet/balance`, {
        method: "GET",
        headers: AuthenticatedHeaders(),
      });
      return await res.json();
    } catch (err) {
      return { message: err.message };
    }
  };

  public static addWalletBalance = async (data) => {
    try {
      const res = await fetch(`${API}/wallet/add-balance`, {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "X-Auth-Token": localStorage.getItem("token"),
          "X-BusinessId": localStorage.getItem("businessId"),
        },
        body: JSON.stringify(data),
      });
      return decryptResponse({ data: await res.json() });
    } catch (err) {
      return { message: err.message };
    }
  };

  public static captureRazerpayOrder = async (paymentId: string, data) => {
    try {
      const res = await fetch(`${API}/wallet/capture-razerpay/${paymentId}`, {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "X-Auth-Token": localStorage.getItem("token"),
          "X-BusinessId": localStorage.getItem("businessId"),
        },
        body: JSON.stringify(data),
      });
      return decryptResponse({ data: await res.json() });
    } catch (err) {
      return { message: err.message };
    }
  };

  public static getActiveEngageBusiness = async () => {
    try {
      const res = await fetch(`${API_V2}/user/engage-business`, {
        method: "GET",
        headers: AuthenticatedHeaders(),
      });
      return await res.json();
    } catch (err) {
      return { message: err.message };
    }
  };

  public static getWACharge = async (data) => {
    try {
      const res = await fetch(`${API}/wallet/wa-sessions-rate`, {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "X-Auth-Token": localStorage.getItem("token"),
          "X-BusinessId": localStorage.getItem("businessId"),
        },
        body: JSON.stringify(data),
      });
      return await res.json();
    } catch (err) {
      return { error: err.message };
    }
  };

  public static allWalletOrders = async () => {
    try {
      const res = await fetch(`${API_V2}/wallet/orders`, {
        method: "GET",
        headers: AuthenticatedHeaders(),
      });
      return await res.json();
    } catch (err) {
      return { message: err.message };
    }
  };
}
