import axios from "axios";
import { API, API_V2 } from "../../Backend";
import decryptResponse from "../../Shared/decryptResponse";
import { AuthenticatedHeaders } from "../../utils/ApiHeaders";

export const assignLeadsToSelectedUserPost = async (data) => {
  try {
    const res = await fetch(`${API_V2}/leads/assign-leads`, {
      method: "PUT",
      headers: AuthenticatedHeaders(),
      body: JSON.stringify(data),
    });

    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const getAllUsers = async (businessId) => {
  try {
    const res = await fetch(`${API}/users`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-BusinessId": businessId || localStorage.getItem("businessId"),
      },
    });
    return decryptResponse({ data: await res.json() });
  } catch (err) {
    return { error: err.message };
  }
};

export const getLeadById = async (leadId) => {
  try {
    const res = await fetch(`${API_V2}/leads/${leadId}`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const getAllLeadsForDownload = async (from, to) => {
  try {
    const res = await fetch(`${API_V2}/leads/download-lead`, {
      method: "POST",
      headers: AuthenticatedHeaders(),
      body: JSON.stringify({ from, to }),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const getAllLeadsForDownloadDepartment = async (from, to) => {
  try {
    const teamId = localStorage.getItem("teamId");
    const departmentId = localStorage.getItem("departmentId");

    const res = await fetch(
      `${API_V2}/leads/download-lead/department/${departmentId || teamId}`,
      {
        method: "POST",
        headers: AuthenticatedHeaders(),
        body: JSON.stringify({ from, to }),
      }
    );

    return await res.json();
  } catch (err) {
    return {
      message: err.message,
    };
  }
};

export const saveLMSLead = async (leadId, data) => {
  try {
    const res = await fetch(`${API_V2}/lead-notes/${leadId}`, {
      method: "POST",
      headers: AuthenticatedHeaders(),
      body: JSON.stringify(data),
    });

    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const fetchAgentReplyStatus = async (leadId, data) => {
  try {
    const res = await axios(`${API}/agent-reply/status`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-BusinessId": localStorage.getItem("businessId"),
      },
    });

    return { data: decryptResponse({ data: await res.data }) };
  } catch (err) {
    return { error: err.message };
  }
};

export const fetchWAMessageData = async () => {
  try {
    const res = await axios(`${API_V2}/chatbots/setting`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });

    return res.data;
  } catch (err) {
    return { message: err.message };
  }
};
