import Axios from "axios";
import { AuthenticatedHeaders } from "utils/ApiHeaders";
import { API, API_V2 } from "../../../Backend";
import * as actions from "../actionTypes";
import { cancelLoading, error } from "../main";

export const clearAllDepartmentLeads = () => {
  return {
    type: actions.CLEAR_ALL_DEPARTMENT_LEADS,
  };
};

export const clearDepartmentLeads = () => {
  return {
    type: actions.CLEAR_DEPARTMENT_LEADS,
  };
};

export const resetDepartmentPageCount = () => {
  return {
    type: actions.RESET_DEPARTMENT_PAGE_COUNT,
  };
};
export const resetDepartmentLeadCount = () => {
  return {
    type: actions.RESET_DEPARTMENT_LEAD_COUNT,
  };
};

export const getDepartmentLeads = (
  page,
  tab,
  { filter },
  toastMessage,
  cancelToken
) => {
  return async (dispatch) => {
    dispatch(cancelLoading());

    dispatch({ type: actions.START_LOADING_DEPARTMENT });

    if (typeof cancelToken != typeof undefined) {
      cancelToken.cancel("Operation canceled due to new request.");
    }

    let source = Axios.CancelToken.source();

    dispatch({ type: actions.GET_CANCEL_TOKEN, cancelToken: source });

    const teamId = localStorage.getItem("teamId");
    const departmentId = localStorage.getItem("departmentId");

    Axios({
      method: "POST",
      url: `${API_V2}/leads/department/all/${
        teamId || departmentId
      }?page=${page}&limit=50&tab=${tab}`,
      headers: AuthenticatedHeaders(),
      data: { filter },
      cancelToken: source.token,
    })
      .then((res) => {
        let data = res.data;

        if (!data.success) {
          dispatch({
            type: actions.HAS_MORE_DEPARTMENT,
            hasMore: false,
          });

          return dispatch({
            type: actions.ERROR_DEPARTMENT,
            error: data.message,
          });
        }

        dispatch({
          type: actions.HAS_MORE_DEPARTMENT,
          hasMore: data.payload.data.length > 0,
        });

        if (tab === "all") {
          dispatch({
            type: actions.GET_ALL_DEPARTMENT_LEADS,
            leads: data.payload.data,
            tabName: tab,
            leadCount: data.payload.meta.total,
            page: data.payload.meta.current_page + 1,
            departmentName: "name",
          });
        } else {
          dispatch({
            type: actions.GET_DEPARTMENT_LEADS,
            leads: data.payload.data,
            tabName: tab,
            leadCount: data.payload.meta.total,
            page: data.payload.meta.current_page + 1,
            departmentName: "name",
          });
        }

        return dispatch({ type: actions.CANCEL_LOADING_DEPARTMENT });
      })
      .catch((err) => {
        if (Axios.isCancel(err)) return;
        dispatch({
          type: actions.HAS_MORE_DEPARTMENT,
          hasMore: false,
        });

        dispatch(error(err.message));

        toastMessage.setErrorMessage(err.message);
        return dispatch({ type: actions.ERROR_DEPARTMENT, error: err.message });
      });
    return () => cancelToken && cancelToken();
  };
};

export const addDepartmentLeads = (leads) => {
  return {
    type: actions.ADD_DEPARTMENT_LEADS,
    leads: leads,
  };
};
