const userEventSlice = createSlice({
  name: "intersect",
  initialState: {
    events: [],
    userEvent: [],
    // events: [...GlobalEventList],
    // userEvent: [],
  },
  reducers: {
    addUserEvent: (state, action) => {
      const data = { ...action.payload };
      const result = isAlreadyPresent(
        JSON.stringify(state, undefined, 2),
        data
      );

      if (result.length > 0) {
        const indexToBeUpdated = findIndexForElement(
          JSON.stringify(state, undefined, 2),
          result[0].day
        );

        const slicedData = state.events.slice();
        const { event, date, day } = action.payload;
        if (slicedData[indexToBeUpdated]["isMultipleEvent"]) {
          slicedData[indexToBeUpdated]["mutipleEvents"] = [
            ...slicedData[indexToBeUpdated["multipleEvents"]],
            {
              day,
              date,
              ...event,
            },
          ];
        } else {
          slicedData[indexToBeUpdated]["isMultipleEvent"] = true;
          slicedData[indexToBeUpdated]["multipleEvents"] = [
            {
              day,
              date,
              ...event,
            },
          ];
        }

        slicedData.sort((a, b) => new Date(a.date) - new Date(b.date));
        state.events = slicedData;
      } else {
        if (action.payload.type === "FETCH") {
          state.events = [];
        }

        const appendedState = [...state.events, ...action.payload.eventsData];

        appendedState.sort((a, b) => new Date(a.date) - new Date(b.date));

        state.events = appendedState;
      }
    },
    updateEvent: (state, action) => {
      const { day, toBeUpdatedWith } = action.payload;
      const indexToBeUpdated = findIndexForElement(
        JSON.stringify(state, undefined, 2),
        day
      );
      const slicedData = state.events.slice();
      slicedData[indexToBeUpdated] = toBeUpdatedWith;
      slicedData.sort((a, b) => new Date(a.date) - new Date(b.date));
      state.events = slicedData;

      //if you wanted to update the event with date
      //   const indexToBeUpdated = findIndexForElement(
      //     JSON.stringify(state, undefined, 2),
      //     date
      //   );
    },
    deleteEvent: (state, action) => {
      const { day } = action.payload;
      const indexToBeDeleted = findIndexForElement(
        JSON.stringify(state, undefined, 2),
        day
      );
      const slicedData = state.events.slice();
      slicedData.splice(indexToBeDeleted, 1);
      slicedData.sort((a, b) => new Date(a.date) - new Date(b.date));
      state.events = slicedData;
    },
  },
});

export const { addUserEvent, deleteEvent, updateEvent } =
  userEventSlice.actions;

export default userEventSlice.reducer;
