import * as actions from "../actions/actionTypes";

const initialState = {
  user: null,
  payments: [],
};

const getUserDetail = (state, action) => {
  return {
    ...state,
    user: action.user,
  };
};
const storeUserPayments = (state, action) => {
  return {
    ...state,
    payments: action.payments,
  };
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.GET_USER_DETAIL:
      return getUserDetail(state, action);
    case actions.STORE_USER_PAYMENTS:
      return storeUserPayments(state, action);
    default:
      return state;
  }
};

export default reducer;
