/** @format */

import axios from "axios";
import { useEffect } from "react";
import { Route, Redirect } from "react-router-dom";

import { useApp } from "contexts/AppProvider";
import { API, API_V2 } from "Backend";

let cancelToken;

const PrivatePermitRoute = ({ component: Component, data, ...rest }) => {
  const { permission } = useApp();
  useEffect(() => {
    let source = axios.CancelToken.source();

    if (typeof cancelToken != typeof undefined) {
      cancelToken.cancel("Operation canceled due to new request.");
    }

    cancelToken = source;

    axios({
      url: `${API_V2}/auth/check`,
      method: "GET",
      headers: {
        // Accept: "application/json",
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
      cancelToken: source.token,
    })
      .then((res) => {
        if (res.data) {
          if (res.data.error) {
            cancelToken && cancelToken();
            throw Error(res.data.error.message ?? "");
          }
          return res.data.logged_in;
        }
      })
      .catch((err) => {
        return window.open("/signout", "_self");
      });
  }, []);

  return (
    <Route
      {...rest}
      render={(props) =>
        localStorage.getItem("token") ? (
          permission === undefined ? (
            <Component {...props} {...data} />
          ) : permission.canViewAllSettings ? (
            <Component {...props} {...data} />
          ) : (
            <Redirect to="/unauthorized" />
          )
        ) : (
          <Redirect to="/login" />
        )
      }
    />
  );
};

export default PrivatePermitRoute;
