import { Dispatch } from "redux";
import { fetchLeadNotes } from "../../../api/Lead";
import { handleLeadProfileLoading, storeLeadNotes } from "./reducer";

export const getLeadNotes = ({ leadId, toastMessage }) => {
  return async (dispatch: Dispatch) => {
    try {
      dispatch(handleLeadProfileLoading({ isLoading: true }));

      const res = await fetchLeadNotes({ leadId });
      if (!res.success)
        return dispatch(storeLeadNotes({ lead_notes: [], leadId }));

      return dispatch(storeLeadNotes({ lead_notes: res.payload, leadId }));
    } catch (err) {
      dispatch(storeLeadNotes({ lead_notes: [], leadId }));
      return toastMessage.setErrorMessage(err.message);
    }
  };
};
