import React, { useContext } from "react";
import { connect } from "react-redux";
import * as actions from "../Store/actions/index";

//import { businessCurrentOrder } from "../api/Business";

export const Business = React.createContext();

export const useBusiness = () => {
  return useContext(Business);
};

//const res = await businessCurrentOrder();

const BusinessProfileContext = ({
  children,
  getAllUserBusiness,
  fetchDefaultBusiness,
  allBusiness,
  selectedBusiness,
  switchBusiness,
}) => {
  const value = {
    getAllUserBusiness,
    fetchDefaultBusiness,
    allBusiness,
    selectedBusiness,
    switchBusiness,
    userRole: selectedBusiness?.role || null,
  };

  return <Business.Provider value={value}>{children}</Business.Provider>;
};

const mapStateToProps = (state) => {
  return {
    allBusiness: state.business.allBusiness,
    selectedBusiness: state.business.selectedBusiness,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllUserBusiness: () => dispatch(actions.getUserBusiness()),
    fetchDefaultBusiness: () => dispatch(actions.fetchDefaultBusiness()),
    switchBusiness: (businessId, data, cb) =>
      dispatch(actions.switchBusiness(businessId, data, cb)),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BusinessProfileContext);
