/** @format */

import { updateObject } from "../../Shared/Utility";
/** @format */

import * as actions from "../actions/actionTypes";

const initialState = {
  invitedUsers: null,
  invitation: false,
};

const getAllUserInvites = (state, action) => {
  return updateObject(state, { invitedUsers: action.invitedUsers });
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.GET_ALL_USER_INVITES:
      return getAllUserInvites(state, action);
    default:
      return state;
  }
};

export default reducer;
