import React, { useContext, useState } from "react";

export const LeadDataFieldContext = React.createContext();

export const useLeadDataFields = () => useContext(LeadDataFieldContext);

const LeadDataFieldProvider = ({ children }) => {
  const [allLeadDataFields, setAllLeadDataFields] = useState([]);
  const [editLdfId, setLdfId] = useState(null);
  const [showInviteForm, setShowInviteForm] = React.useState(false);
  const value = {
    allLeadDataFields,
    setAllLeadDataFields,
    editLdfId,
    setLdfId,
    showInviteForm,
    setShowInviteForm,
  };

  return (
    <LeadDataFieldContext.Provider value={value}>
      {children}
    </LeadDataFieldContext.Provider>
  );
};

export default LeadDataFieldProvider;
