import * as actions from "./actionTypes";

export const getPhotos = (photos) => {
  return {
    type: actions.GET_PHOTOS,
    photos: photos,
  };
};

export const addPhoto = (data) => {
  return {
    type: actions.ADD_PHOTO,
    data: data,
  };
};

export const deletePhoto = (id) => {
  return {
    type: actions.DELETE_PHOTO,
    id: id,
  };
};
