import { createContext, useContext, useState } from "react";
import { connect } from "react-redux";
import {
  authSteps,
  checkUserAuthStatus,
  signInWithNumber,
  signWithPassword,
  submitPhoneNumberAuthCode,
} from "../Store/actions";

export const Auth = createContext();

export const useAuth = () => {
  return useContext(Auth);
};

const AuthContext = ({
  signInWithNumber,
  submitPhoneNumberAuthCode,
  isLoggedIn,
  authSteps,
  setAuthStep,
  children,
  checkUserAuthStatus,
  signInWithPassword,
}) => {
  const [currentLoginState, setCurrentLoginState] = useState("Password");

  const [formData, setFormData] = useState({
    number: {
      placeholder: "Email or Mobile No",
      name: "MobileNumber",
      type: "text",
      maxLength: 30,
      minLength: 4,
      value: "",
      id: 1,
      disabled: false,
    },
    otp: {
      placeholder: currentLoginState === "OTP" ? "OTP" : "Password",
      name: currentLoginState === "OTP" ? "OTP" : "Password",
      type: "password",
      maxLength: 6,
      minLength: 6,
      value: "",
      id: 2,
      disabled: authSteps === 0 ? true : false,
    },
  });

  const [resendOTP, setResendOTP] = useState({
    counter: 29,
    disabled: true,
    startTimer: true,
  });

  const value = {
    submitPhoneNumberAuthCode,
    signInWithNumber,
    authSteps,
    setAuthStep,
    isLoggedIn,
    checkUserAuthStatus,
    token: localStorage.getItem("token"),
    formData,
    setFormData,
    signInWithPassword,
    currentLoginState,
    setCurrentLoginState,
    resendOTP,
    setResendOTP,
  };

  return <Auth.Provider value={value}>{children}</Auth.Provider>;
};

const mapStateToProps = (state) => {
  return {
    isLoggedIn: state.auth.isLoggedIn,
    userId: state.auth.userId,
    authSteps: state.auth.currentCount,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    signInWithNumber: (number, toastMessage, showLoader) =>
      dispatch(signInWithNumber(`+${number}`, toastMessage, showLoader)),
    signInWithPassword: (field, password, toastMessage, showLoader) =>
      dispatch(signWithPassword(field, password, toastMessage, showLoader)),
    submitPhoneNumberAuthCode: (otp, toastMessage, showLoader) =>
      dispatch(submitPhoneNumberAuthCode(otp, toastMessage, showLoader)),
    checkUserAuthStatus: () => dispatch(checkUserAuthStatus()),
    setAuthStep: (currentCount) => dispatch(authSteps(currentCount)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AuthContext);
