/** @format */

import * as actions from "../actions/actionTypes";

const initialState = {
  fonts: null,
};

const getFonts = (state, action) => {
  return {
    ...state,
    fonts: action.fonts,
  };
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.GET_FONTS:
      return getFonts(state, action);
    default:
      return state;
  }
};

export default reducer;
