import { motion } from "framer-motion";
import React, { useEffect, useState } from "react";
import classes from "./ResetPasswordPage.module.css";
import Input from "../../../UI/Input/Input";
import Modal2 from "../../../UI/Modal2/Modal2";
import UIButton from "../../../UI/UIButton/UIButton";
import { useToastMessage } from "../../../../contexts";
import { useInterval } from "../../../../hooks/useInterval";
import { sleep, updateObject } from "../../../../Shared/Utility";
import clsx from "clsx";
import firebase from "../../../../firebase";
import { resetPassword } from "../../../../api/Auth";
import { simpleEncrypt } from "../../../../Shared/encryptResponse";

const ResetPasswordPage = ({ open, closePage, mobileNumber }) => {
  const variants = {
    open: { opacity: 1, x: "0%", y: "0%", pointerEvents: "all" },
    closed: { opacity: 0, x: "0%", y: "100%", pointerEvents: "none" },
  };

  const [userData, setUserData] = useState({
    otp: {
      value: "",
      required: true,
      type: "number",
      placeholder: "OTP",
      id: 1,
      name: "otp",
    },
    newPassword: {
      value: "",
      required: true,
      type: "password",
      placeholder: "New Password",
      id: 2,
      readOnly: true,
      name: "new-password",
    },
    confirmPassword: {
      value: "",
      name: "confirm-password",
      required: true,
      type: "password",
      placeholder: "Confirm Password",
      id: 3,
    },
  });

  const {
    setSuccessMessage,
    setErrorMessage,
    disabled,
    setDisabled,
    setInfoMessage,
  } = useToastMessage();

  const toastMessage = useToastMessage();

  const [resendOTP, setResendOTP] = useState({
    counter: 0,
    disabled: false,
    startTimer: false,
  });

  const startTimer = () => {
    signInWithNumber(mobileNumber);

    setResendOTP((prevState) => ({
      ...prevState,
      disabled: true,
      counter: 30,
      startTimer: true,
    }));
  };

  const signInWithNumber = async (number) => {
    number = number;
    if (number.length <= 10) {
      return;
    }

    setDisabled();

    try {
      const confirmationResult = await firebase
        .auth()
        .signInWithPhoneNumber(number, window.recaptchaVerifier);

      window.confirmationResult = confirmationResult;

      setSuccessMessage("OTP has been sent to your mobile number!");
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const submitPhoneNumberAuthCode = async (cb) => {
    if (userData.otp.value.length < 6) {
      return setErrorMessage("Please Enter OTP");
    }

    setDisabled();

    await firebase.auth().setPersistence(firebase.auth.Auth.Persistence.NONE);

    window.confirmationResult
      .confirm(userData.otp.value)
      .then(async (result) => {
        const user = result;
        const token = user.user._lat;

        const userId = firebase.auth().currentUser.uid;

        cb &&
          cb({
            password: userData.newPassword.value,
            confirm_password: userData.confirmPassword.value,
          });
      })
      .catch((error) => {
        return setErrorMessage(error.message);
      });
  };

  const authCaptchaVerifier = () => {
    window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier(
      "captcha-container",
      {
        size: "invisible",
        callback: function (response) {
          signInWithNumber(mobileNumber, toastMessage);
        },
        "expired-callback": function () {
          // Response expired. Ask user to solve reCAPTCHA again.
          // ...
          alert("try again!");
        },
      }
    );
  };

  useEffect(() => {
    if (open) {
      authCaptchaVerifier(mobileNumber);

      startTimer();
    }
  }, [open]);

  useInterval(
    () => {
      setResendOTP((prevState) => ({
        ...prevState,
        disabled: prevState.counter > 0 ? true : false,
        counter: prevState.counter - 1,
        startTimer: prevState.counter > 0 ? true : false,
      }));
    },
    resendOTP.counter >= 0 && resendOTP.startTimer ? 1000 : null
  );

  const handleInput = (event, inputIdentifier) => {
    let updatedFormElement;

    updatedFormElement = updateObject(userData[inputIdentifier], {
      value: event.target.value,
    });

    const updatedOrderForm = updateObject(userData, {
      [inputIdentifier]: updatedFormElement,
    });

    setUserData(updatedOrderForm);

    if (inputIdentifier === "confirmPassword") {
      if (userData.newPassword.value !== event.target.value) {
        setPasswordError(true);
      } else {
        setPasswordError(false);
      }
    }
  };

  const [isPasswordError, setPasswordError] = useState(false);

  const onFormSubmit = (e) => {
    e.preventDefault();

    submitPhoneNumberAuthCode(async ({ password, confirm_password }) => {
      try {
        const res = await resetPassword({ password, confirm_password });
        if (!res.success) {
          return setErrorMessage(
            res.messages ? res.messages.errors[0].message : res.message
          );
        }

        localStorage.removeItem("token");

        setSuccessMessage("You have Successfully changed your password");

        await sleep(555);

        setInfoMessage("Wait, We are redirecting you to login page!");

        firebase.auth().signOut();

        return window.open("/signout", "_self");
      } catch (err) {
        return setErrorMessage(err.message);
      }
    });
  };

  return (
    <motion.div
      initial={{ opacity: 0, x: "100%", y: "0%", pointerEvents: "none" }}
      transition={{ duration: 0.5, ease: [0.43, 0.13, 0.23, 0.96] }}
      variants={variants}
      animate={open ? "open" : "closed"}
      className={classes.Container}
    >
      <Modal2 close={closePage} title="Change Password" />

      <form className={classes.Form} onSubmit={onFormSubmit}>
        {/* OTP */}
        <Input
          type={userData.otp.type}
          name={userData.otp.name}
          value={userData.otp.value}
          inputChangedHandler={handleInput}
          required={userData.otp.required}
          id={"otp"}
          placeholder={userData.otp.placeholder}
          maxLength={6}
          minLength={6}
          readOnly={false}
        />

        {/* helper text */}
        <div
          style={{ marginTop: "-10px" }}
          className="d-flex justify-content-between "
        >
          <p className={classes.HelperText}>sent on: {mobileNumber}</p>
          <p
            style={{
              display: "flex",
              justifyContent: "flex-end",
              width: "max-content",
              fontSize: "15px",
              opacity: resendOTP.disabled ? 0.3 : 0.6,
              pointerEvents: resendOTP.disabled ? "none" : "all",
              cursor: resendOTP.disabled ? "not-allowed" : "pointer",
            }}
            onClick={() => {
              startTimer();
            }}
          >
            Resend OTP
            {resendOTP.counter < 30 &&
              resendOTP.counter > 0 &&
              ` in ${resendOTP.counter}s`}
          </p>
        </div>

        {/* New Password */}
        <Input
          type={userData.newPassword.type}
          name="new-password"
          value={userData.newPassword.value}
          inputChangedHandler={handleInput}
          required={true}
          id={"newPassword"}
          placeholder={userData.newPassword.placeholder}
        />

        {/* Repeat password */}
        <Input
          type={userData.confirmPassword.type}
          name={userData.confirmPassword.name}
          value={userData.confirmPassword.value}
          inputChangedHandler={handleInput}
          required={true}
          id={"confirmPassword"}
          placeholder={userData.confirmPassword.placeholder}
          readOnly={false}
        />
        {isPasswordError && (
          <div style={{ marginTop: "-12px" }}>
            <p className={clsx(classes.HelperText, classes.ErrorText)}>
              confirm password doesn't match with new password.
            </p>
          </div>
        )}

        <UIButton
          disabled={disabled || isPasswordError}
          type="submit"
          svgStyle={{ marginLeft: "10px" }}
        >
          Save New Password
        </UIButton>
      </form>
      <div id="captcha-container"></div>
    </motion.div>
  );
};

export default ResetPasswordPage;
