import React from "react";
import { Button } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import clsx from "clsx";

const styles = {
  root: {
    marginLeft: "10%",
    display: "flex",
    justifyContent: "space-between",
    padding: "15px 20px 15px 20px",
    borderRadius: "8px",
    boxShadow: "0 8px 8px 0 rgba(50, 197, 255, 0.1)",
    fontSize: "16px",
    fontWeight: "bold",
    background: "linear-gradient(45deg, #12B0EE 30%, #12B0EE 90%)",
    border: 0,
    color: "white",
    height: 48,
    textTransform: "none",
  },
};
const CustomButton = ({
  classes,
  children,
  className,
  style,
  onClick,
  disabled,
  showLoader = false,
  ...props
}) => {
  return (
    <Button
      onClick={onClick}
      disabled={disabled}
      style={style}
      type="submit"
      className={clsx(classes.root, className)}
      {...props}
    >
      {showLoader && (
        <i style={{ marginLeft: "5px" }} className="fa fa-spinner fa-spin"></i>
      )}
      {children}
    </Button>
  );
};

export default withStyles(styles)(CustomButton);
