import React, { useContext } from "react";

export const WaitingLoader = React.createContext();

export const useWaitingLoader = () => useContext(WaitingLoader);

const WaitingLoaderContext = ({ children }) => {
  const [open, setOpen] = React.useState(false);

  const openLoader = () => {
    setOpen(true);
  };

  const closeLoader = () => {
    setOpen(false);
  };

  const value = {
    open,
    setOpen,
    openLoader,
    closeLoader,
  };

  return (
    <WaitingLoader.Provider value={value}>{children}</WaitingLoader.Provider>
  );
};

export default WaitingLoaderContext;
