import React, { useContext, useState } from "react";

//import { getUserProfile} from "../api/user";
import { useToastMessage } from "../contexts";

import { getBusinessInvoices } from "../api/Invoice";
import WalletAPI from "../api/Wallet";

export const Invoice = React.createContext();

export const useInvoice = () => useContext(Invoice);

const InvoiceContext = ({ children }) => {
  const { setErrorMessage } = useToastMessage();

  const [invoices, setInvoices] = useState([]);
  const [walletOrders, setWalletOrders] = useState([]);

  const [walletBalance, setWalletBalance] = useState({
    balance: 0,
    currency: "INR",
  });

  const getInvoices = async () => {
    const businessId = localStorage.getItem("businessId");
    if (businessId) {
      const res = await getBusinessInvoices();
      if (!res.success) {
        setErrorMessage(res.message);
      } else {
        setInvoices(res.payload);
      }
    }
  };

  const getWalletBalance = async () => {
    const result = await WalletAPI.getWalletBalance();
    if (!result.success) {
      setErrorMessage(result.message);
    } else {
      setWalletBalance({
        balance: result.payload.balance,
        currency: result.payload.currency,
      });
    }
  };

  const getWalletOrders = async () => {
    const businessId = localStorage.getItem("businessId");
    if (businessId) {
      const res = await WalletAPI.allWalletOrders();
      if (!res.success) {
        setErrorMessage(res.message);
      } else {
        setWalletOrders(res.payload);
      }
    }
  };

  const value = {
    invoices,
    setInvoices,
    getInvoices,
    walletBalance,
    setWalletBalance,
    getWalletBalance,
    walletOrders,
    setWalletOrders,
    getWalletOrders,
  };

  return <Invoice.Provider value={value}>{children}</Invoice.Provider>;
};

export default InvoiceContext;
