import React, { useContext, useEffect, useRef, useState } from "react";
import { getBusinessById } from "../api/Dashboard";
import { ToastProvider } from "react-toast-notifications";
import { CustomButtonToast } from "../Components/UI/Toasts/Toast";
import { connect } from "react-redux";
import * as actions from "../Store/actions/index";

export const ProfileContext = React.createContext();

export const useBusinessProfile = () => {
  return useContext(ProfileContext);
};

const BusinessProfileContext = ({
  children,
  getAllUserBusiness,
  fetchDefaultBusiness,
  allBusiness,
  selectedBusiness,
}) => {
  const [businessProfile, setBusinessProfile] = useState(null);
  const [businessId, setBusinessId] = useState(null);

  const getBusinessByIdRef = useRef(() => {});

  const [toast, setToast] = useState({
    error: null,
    success: null,
    disabled: false,
  });

  getBusinessByIdRef.current = () => {
    setToast({ ...toast, error: null, success: null, disabled: true });

    getBusinessById(businessId)
      .then((data) => {
        if (data.error) {
          setToast({
            ...toast,
            success: null,
            error: data.error,
            disabled: false,
          });
        } else {
          setBusinessProfile(data.data);
        }
      })
      .catch((err) => {
        setToast({
          ...toast,
          success: null,
          error: err,
          disabled: false,
        });
      });
  };

  useEffect(() => {
    if (!businessProfile && businessId) {
      getBusinessByIdRef.current();
    }
  }, [businessProfile, businessId]);

  const value = {
    businessProfile: businessProfile,
    setBusinessId,
    getAllUserBusiness,
    fetchDefaultBusiness,
    allBusiness,
    selectedBusiness,
  };

  return (
    <ProfileContext.Provider value={value}>
      {children}
      <ToastProvider>
        <CustomButtonToast
          show={toast.error !== null || toast.success !== null}
          appearance={
            toast.error ? "error" : toast.success ? "success" : "info"
          }
          message={toast.error || toast.success}
        />
      </ToastProvider>
    </ProfileContext.Provider>
  );
};

const mapStateToProps = (state) => {
  return {
    allBusiness: state.business.allBusiness,
    selectedBusiness: state.business.selectedBusiness,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllUserBusiness: () => dispatch(actions.getUserBusiness()),
    fetchDefaultBusiness: () => dispatch(actions.fetchDefaultBusiness()),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BusinessProfileContext);
