import React, { createContext, useContext, useState } from "react";

export const LeadDistributionRulesContext = createContext();

export const useLeadDistributionRules = () =>
  useContext(LeadDistributionRulesContext);

const LeadDistributionRulesProvider = ({ children }) => {
  const [allLeadRules, setAllLeadRules] = useState([]);
  const [showCreateForm, toggleCreateForm] = useState(false);
  const [reloadRules, doReloadRules] = useState(false);
  const value = {
    allLeadRules,
    setAllLeadRules,
    showCreateForm,
    toggleCreateForm,
    reloadRules,
    doReloadRules,
  };

  return (
    <LeadDistributionRulesContext.Provider value={value}>
      {children}
    </LeadDistributionRulesContext.Provider>
  );
};

export default LeadDistributionRulesProvider;
