/** @format */

import clsx from "clsx";
import React from "react";
import { withRouter } from "react-router-dom";
import classes from "./BackBtn.module.css";

const BackBtn = ({ className, ...props }) => {
  return (
    <div
      onClick={() => {
        if (props.close) {
          return props.close();
        }

        if (props.setOpen) {
          return props.setOpen(false);
        }

        if (props.link) {
          return props.history.push(props.link);
        } else {
          return props.history.goBack();
        }
      }}
      className={clsx(classes.Back_Btn, className)}
    >
      <svg
        width="9"
        height="14"
        viewBox="0 0 9 14"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M8 1L1.8858 6.24074C1.42019 6.63984 1.42019 7.36016 1.8858 7.75926L8 13"
          stroke="#24272c"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="bevel"
        />
      </svg>
    </div>
  );
};

export default withRouter(BackBtn);
