import { API_V2 } from "../../Backend";
import { AuthenticatedHeaders } from "./../../utils/ApiHeaders";

class MainMenuAPI {
  async getMainMenu() {
    try {
      const res = await fetch(`${API_V2}/mainmenus/products`, {
        method: "GET",
        headers: AuthenticatedHeaders(),
      });
      return await res.json();
    } catch (err) {
      return { message: err.message };
    }
  }
  async getSubMainMenu(id) {
    try {
      const res = await fetch(`${API_V2}/mainmenus/product-menu/${id}`, {
        method: "GET",
        headers: AuthenticatedHeaders(),
      });
      return await res.json();
    } catch (err) {
      return { message: err.message };
    }
  }
}

export default MainMenuAPI;
