import * as actions from "../actionTypes";
import firebase from "../../../firebase";
import { cancelLoading, error, startLoading } from "../main";
import { validateCoupon } from "../../../api/Coupon";
import { getPlanDetail } from "../../../api/Plan";
import { checkUserAuth, login } from "../../../api/Auth";
import { getBusinessProfile } from "api/Dashboard";

const auth = firebase.auth();

export const authStart = () => {
  return {
    type: actions.AUTH_START,
  };
};

export const authFail = (error) => {
  return {
    type: actions.AUTH_FAIL,
    error: error,
  };
};

export const setAuthRedirectPath = (path) => {
  window.location.href = path;
  return {
    type: actions.SET_AUTH_REDIRECT_PATH,
    path: path,
  };
};

export const setDefaultRedirectPath = (path) => {
  return {
    type: actions.SET_DEFAULT_REDIRECT_PATH,
    path: path,
  };
};

export const authSuccess = (token, userId) => {
  return {
    type: actions.AUTH_SUCCESS,
    token,
    userId,
  };
};

export const getUserCollectionId = (collectionId) => {
  return {
    type: actions.GET_USER_COLLECTION_ID,
    collectionId: collectionId,
  };
};

export const authState = (isLoggedIn) => {
  return {
    type: actions.AUTH_STATE,
    isLoggedIn: isLoggedIn,
  };
};

export const authSteps = (currentCount) => {
  return {
    type: actions.AUTH_STEPS,
    currentCount: currentCount,
  };
};

export const applyCoupon = (couponDetail) => {
  return {
    type: actions.APPLY_COUPON,
    // discounted_amount: discounted_amount,
    // discount: discount,
    couponDetail: couponDetail,
  };
};

// sends otp
export const signInWithNumber = (number, toastMessage, showLoader) => {
  return async (dispatch) => {
    if (number.length <= 10) {
      return;
    }

    const { setSuccessMessage, setDisabled, setInfoMessage } = toastMessage;

    showLoader && dispatch(startLoading());
    setDisabled();
    dispatch(authStart());

    try {
      const confirmationResult = await auth.signInWithPhoneNumber(
        number,
        window.recaptchaVerifier
      );
      dispatch(authSteps(1));

      window.confirmationResult = confirmationResult;

      dispatch(cancelLoading());

      return setSuccessMessage("OTP has been sent to your mobile number!");
    } catch (err) {
      setInfoMessage("Please refresh the page and Try again!");
      dispatch(authFail(err.message));
      dispatch(cancelLoading());
    }
  };
};

export async function loginHelper({
  field,
  password,
  dispatch,
  setErrorMessage,
  setSuccessMessage,
}) {
  try {
    const res = await login(field, password);
    localStorage.setItem("token", res.token);
    localStorage.setItem("userId", res.user.id);
    const business = await getBusinessProfile();
    dispatch(
      setAuthRedirectPath(business?.payload?.default_url || "/business-setup")
    );
    setSuccessMessage("Successfully loggedIn!");
    return dispatch(authSuccess(res.token, res.user.id));
  } catch (err) {
    let errpr_msg = err.message.includes("reading 'id'")
      ? "It seems you entered a wrong user id or password, please try again."
      : err.message;
    return setErrorMessage(errpr_msg);
  }
}

export const signWithPassword = (field, password, toastMessage, showLoader) => {
  return async (dispatch) => {
    const { setSuccessMessage, setErrorMessage, setDisabled } = toastMessage;
    showLoader && dispatch(startLoading());
    setDisabled();
    dispatch(authStart());

    try {
      // if (parseInt(field)) {
      //   throw Error("Can't login with mobile number.");
      // } else {
      return loginHelper({
        field,
        password,
        setErrorMessage,
        setSuccessMessage,
        dispatch,
      });
      // }
    } catch (err) {
      setErrorMessage(err.message);
      return dispatch(authFail(err.message));
    }
  };
};

// verifies otp
export const submitPhoneNumberAuthCode = (
  otp,
  toastMessage,
  showLoader,
  type
) => {
  return async (dispatch) => {
    const { setSuccessMessage, setErrorMessage, setDisabled, setInfoMessage } =
      toastMessage;
    try {
      showLoader && dispatch(startLoading());

      setDisabled();

      firebase.auth().setPersistence(firebase.auth.Auth.Persistence.NONE);

      await window.confirmationResult
        .confirm(otp)
        .then((result) => {
          const user = result;
          const token = user.user._lat;
          const userId = firebase.auth().currentUser.uid;
          dispatch(authSteps(2));

          /*           return loginHelper({
            token,
            userId,
            setErrorMessage,
            setSuccessMessage,
            dispatch,
          }); */
        })
        .catch((error) => {
          if (error.code === "auth/invalid-verification-code") {
            setInfoMessage("Please Enter valid OTP");
          } else {
            setErrorMessage(error.message);
          }
          dispatch(cancelLoading());
          dispatch(authFail(error));
        });
    } catch (err) {
      return setErrorMessage("Something Went Wrong!");
    }
  };
};

export const logout = () => {
  localStorage.removeItem("token");
  localStorage.removeItem("userId");
  return {
    type: actions.AUTH_LOGOUT,
  };
};

export const validateCouponCode = (couponCode) => {
  return (dispatch) => {
    dispatch(startLoading());

    const planId = JSON.parse(localStorage.getItem("planId"));

    validateCoupon({
      couponCode: couponCode,
      uid: localStorage.getItem("userId"),
    })
      .then((response) => {
        if (response.error) {
          dispatch(
            applyCoupon({
              discounted_amount: null,
              discount: null,
              error: response.error,
            })
          );
          dispatch(cancelLoading());
        } else {
          const couponType = response.data.type;
          getPlanDetail(planId).then((plan) => {
            let couponAmount = response.data.amount;
            let amount = plan.data.amount;
            let discounted_amount;
            let discount;
            let promoCode;
            if (couponType !== "flat") {
              discount = (couponAmount / 100) * amount;
              discounted_amount = amount - discount;
              promoCode = couponCode;

              dispatch(cancelLoading());
            } else {
              discount = couponAmount;
              discounted_amount = amount - couponAmount;
              promoCode = couponCode;
              dispatch(cancelLoading());
            }
            dispatch(cancelLoading());
            return dispatch(
              applyCoupon({
                discounted_amount: discounted_amount,
                discount: discount,
                error: null,
                promoCode: promoCode,
              })
            );
          });
        }
      })
      .catch((err) => {
        dispatch(error(err));
      });
  };
};

export const resetCouponField = () => {
  return (dispatch) => {
    dispatch(
      applyCoupon({
        discounted_amount: null,
        discount: null,
        error: null,
      })
    );
  };
};

export const validatePlan = () => {
  return (dispatch) => {
    dispatch(startLoading());
    if (localStorage.getItem("planId") == null) {
      dispatch(authSteps(3));
      dispatch(cancelLoading());
    } else {
      dispatch(authSteps(4));
      dispatch(cancelLoading());
    }
  };
};

export const checkUserAuthStatus = () => {
  return (dispatch) => {
    dispatch(startLoading());
    if (localStorage.getItem("token")) {
      checkUserAuth()
        .then((data) => {
          if (!data.success) {
            dispatch(cancelLoading());
            return dispatch(logout());
          } else {
            if (data.email === "") {
              dispatch(cancelLoading());
              //dispatch(setAuthRedirectPath("/sign-up"));
            } else {
              dispatch(cancelLoading());
              dispatch(setAuthRedirectPath(data.redirect ?? "/engage/chat"));
            }
          }
        })
        .catch((err) => {
          dispatch(cancelLoading());
          return dispatch(error(err));
        });
    } else {
      return dispatch(cancelLoading());
    }
  };
};
