import { useState, createContext, useEffect } from "react";

export const SocialMediaSetupContext = createContext();

const SocialMediaSetupContextProvider = ({ children }) => {
  // component states
  const [FBPages, setFBPages] = useState([]);

  useEffect(() => {
    // console.log("FBPages in context: ", FBPages);
  }, [FBPages]);

  return (
    <SocialMediaSetupContext.Provider value={{ FBPages, setFBPages }}>
      {children}
    </SocialMediaSetupContext.Provider>
  );
};

export default SocialMediaSetupContextProvider;
