import { AuthenticatedHeaders } from "utils/ApiHeaders";
import { API_V2 } from "../../Backend";

export const getBusinessProfile = async () => {
  try {
    const res = await fetch(`${API_V2}/business/default-business`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });

    return await res.json();
  } catch (err) {
    return {
      message: err.message,
    };
  }
};
export const getAllBusiness = async () => {
  try {
    const res = await fetch(`${API_V2}/business/all`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const getBusinessById = async (businessId) => {
  try {
    const res = await fetch(`${API_V2}/business`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("token")}`,
        "X-BusinessId": businessId,
      },
    });

    return await res.json();
    // return await res.json();
  } catch (err) {
    return {
      message: err.message,
    };
  }
};
