import { useEffect, useRef, useState } from "react";

import { checkUserAuth } from "api/Auth";
import { getBusinessProfile } from "api/Dashboard";
import { Redirect } from "react-router-dom";

const DefaultRedirect = () => {
  const [isAuth, setAuth] = useState();
  const [redirectTo, setRedirect] = useState("");
  const checkAuthAndRedirect = useRef(async () => {
    if (localStorage.getItem("token")) {
      const authData = await checkUserAuth();
      if (authData.logged_in) {
        setAuth(true);
        const business = await getBusinessProfile();
        if (business.success) {
          setRedirect(business.payload.default_url);
        } else {
          setRedirect("/login");
        }
      } else {
        setAuth(false);
        setRedirect("/login");
      }
    } else {
      setAuth(false);
      setRedirect("/login");
    }
  });
  useEffect(() => {
    checkAuthAndRedirect.current();
  }, []);

  if (isAuth === undefined) {
    return null;
  }
  if (!isAuth && redirectTo !== "") {
    return <Redirect to={redirectTo} />;
  }

  return <Redirect to={redirectTo} />;
};

export default DefaultRedirect;
