import { createSlice } from "@reduxjs/toolkit";

const strategySlice = createSlice({
  name: "intersect",
  initialState: {
    strategyStatus: {},
    colorAvailable: [
      {
        name: "Green",
        hexCode: "#4BAEA3",
      },

      {
        name: "Amber",
        hexCode: "#FDB950",
      },
      {
        name: "Magento",
        hexCode: "#D48BE8",
      },
      {
        name: "Blue",
        hexCode: "#6495D9",
      },
      {
        name: "Lemon",
        hexCode: "#DADA6A",
      },
      {
        name: "Lavendar",
        hexCode: "#9B8BDF",
      },
      {
        name: "Cyber",
        hexCode: "#6DDDF5",
      },
    ],
    strategyList: [
      // {
      //   name: "Event",
      //   // strategyID: "ughts",
      //   color: {
      //     name: "red",
      //     value: "#c85b70",
      //   },
      //   desc: "Daily motivating quotes as a brand uplifting Exercise",
      //   isDefault: true,
      // },
    ],
  },
  reducers: {
    addStrategy: (state, action) => {
      const { name, color } = action.payload;

      const newAvailableColorList = state.colorAvailable.filter(
        (c) => c.name !== color.name
      );

      state.colorAvailable = newAvailableColorList;
      state.strategyStatus[name] = true;
      state.strategyList = [...state.strategyList, { ...action.payload }];
    },
    updateCheckBox: (state, action) => {
      const { name, value } = action.payload;
      state.strategyStatus[name] = value;
    },
    updateCheckBoxAll: (state, action) => {
      const { check } = action.payload;

      const updateStrategyCheckStatus = {};

      Object.keys(state.strategyStatus).forEach((key) => {
        updateStrategyCheckStatus[key] = check;
      });

      state.strategyStatus = updateStrategyCheckStatus;
    },
    deleteStrategy: (state, action) => {
      const { strategyID, color } = action.payload;
      let copyAvailableColors = state.colorAvailable;

      copyAvailableColors.push({
        name: color.name,
        hexCode: color.value,
      });

      state.colorAvailable = copyAvailableColors;

      const prevStrategyList = state.strategyList.slice();
      const newStrategyList = prevStrategyList.filter(
        (e) => e.strategyID !== strategyID
      );
      state.strategyList = newStrategyList;
    },
    updateStrategy: (state, action) => {
      const { strategyID, name, color } = action.payload;
      const newAvailableColorList = state.colorAvailable.filter(
        (c) => c.name !== color.name
      );
      if (newAvailableColorList.length > 0) {
        state.colorAvailable = newAvailableColorList;
      }
      const indexToUpdate = state.strategyList.findIndex(
        (e) => e.strategyID === strategyID
      );
      const copyState = state.strategyList.slice();
      copyState[indexToUpdate] = { ...action.payload };
      state.strategyStatus[name] = true;
      state.strategyList = copyState;
    },
    fetchStrategies: (state, action) => {
      const { data } = action.payload;

      // const eventStrategy = {
      //   name: "Event",
      //   // strategyID: "ughts",
      //   color: {
      //     name: "red",
      //     value: "#c85b70",
      //   },
      //   desc: "Daily motivating quotes as a brand uplifting Exercise",
      //   isDefault: true,
      // };

      // state.strategyList = [eventStrategy, ...data];
      state.strategyList = [...data];

      const updatedStrategyStatusState = {};

      data.forEach((strategy) => {
        updatedStrategyStatusState[strategy.name] = true;
      });

      state.strategyStatus = updatedStrategyStatusState;
    },
    updateStrategyStatus: (state, action) => {
      const { data } = action.payload;

      state.strategyStatus = { ...data };
    },
  },
});

export const {
  addStrategy,
  updateStrategy,
  deleteStrategy,
  updateCheckBox,
  updateCheckBoxAll,
  fetchStrategies,
  updateStrategyStatus,
} = strategySlice.actions;

export default strategySlice.reducer;
