import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";

const useStyles = makeStyles((theme) => ({
  formControl: {
    padding: "5px",
    width: "100%",
    background: "#f2f3f7",
    borderRadius: "8px",
    "& .MuiInput-root": {
      margin: "0 auto",
      width: "100% !important",
    },
    "& label + .MuiInput-formControl": {
      marginTop: "16px !important",
    },
    "& .MuiInputLabel-formControl": {
      top: "0",
      left: "5%",
      //   transform: "translate(0, 100%) scale(1)",
    },
    "& .MuiInput-underline:before": {
      content: "none",
    },
    "& .MuiSelect-select.MuiSelect-select": {
      paddingRight: "24px",
      paddingLeft: "5%",
    },
  },
}));

export default function GroupedSelect(props) {
  const classes = useStyles();

  return (
    <div>
      <FormControl className={classes.formControl}>
        <InputLabel htmlFor="grouped-select">{props.id}</InputLabel>
        <Select
          required
          value={props.value}
          onChange={(e) => {
            if (props.id === "Industry") {
              props.handleIndustryValue(e.target.value);
            } else if (props.id === "Time Zone") {
              props.handleTimezoneValue(e.target.value);
            } else if (props.id === "Country") {
              props.handleCountryValue(e.target.value);
            } else if (props.id === "State") {
              props.handleStateValue(e.target.value);
            }
          }}
          id="grouped-select"
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          {props.id === "Industry" &&
            props.list.map((e) => (
              <MenuItem key={e} value={e}>
                {e}
              </MenuItem>
            ))}
          {props.id === "Time Zone" &&
            props.list.map((e, i) => (
              <MenuItem key={i} value={e.label}>
                {e.label}
              </MenuItem>
            ))}

          {props.id === "Country" &&
            props.list.map((e, i) => (
              <MenuItem key={i} value={e.country}>
                {e.country}
              </MenuItem>
            ))}

          {props.id === "State" &&
            props.list.map((e, i) => (
              <MenuItem key={i} value={e.state}>
                {e.state}
              </MenuItem>
            ))}
        </Select>
      </FormControl>
    </div>
  );
}
