/** @format */

import { API, API_V2 } from "../../Backend";
import { AuthenticatedHeaders } from "utils/ApiHeaders";

export const getBusinessInvoices = async () => {
  try {
    const res = await fetch(`${API_V2}/invoices/all-invoices`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return await res.json();
  } catch (err) {
    return {
      message: err.message,
    };
  }
};

export const createInvoicePdf = async (invoiceId) => {
  try {
    const res = await fetch(`${API_V2}/invoices/download-invoice/${invoiceId}`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return await res;
  } catch (err) {
    return { message: err.message };
  }
};

