export const downloadInvoicePdf = async (filename) => {
  console.log(filename);
  try {
    const res = await fetch(`${API}/invoice/download-invoice/${filename}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/pdf",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-BusinessId": localStorage.getItem("businessId"),
      },
    });

    return res;
  } catch (err) {
    return {
      message: err.message,
    };
  }
};

export const showInvoiceById = async (id) => {
  try {
    const res = await fetch(`${API_V2}/invoices/${id}`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return res.json();
  } catch (err) {
    return {
      message: err.message,
    };
  }
};
