/** @format */

const { API, API_V2 } = require("../../Backend");

export const getPlanDetail = (planId) => {
  return fetch(`${API}/detail/plan/${planId}`, {
    method: "GET",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      "X-Auth-Token": localStorage.getItem("token"),
    },
  })
    .then((response) => response.json())
    .catch((err) => console.log(err));
};

export const getPlans = async () => {
  try {
    console.log("helo");
    const res = await fetch(`${API_V2}/plans/web-plan`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "Content-Type": "application/json",
      },
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const getCategoryPlan = async (category, plan_type) => {
  try {
    const res = await fetch(`${API}/plan/${category}/${plan_type}`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "Content-Type": "application/json",
      },
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};
